import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { UserServiceProvider } from '../../providers/user-service/user-service'
import { InsuranceServiceProvider } from '../../providers/insurance-service/insurance-service'
import { UtilityProvider } from '../../providers/utility/utility'

@Component({
  selector: 'page-family-share',
  templateUrl: 'family-share.html',
})
export class FamilySharePage {
  familyMemberList = [];
  shareFamilyMemberIds = [];
  policyId;
  constructor(public navCtrl: NavController, public navParams: NavParams, private userAPISerivce: UserServiceProvider, private insuranceAPI: InsuranceServiceProvider, private util: UtilityProvider) {
    this.policyId = navParams.get('id')
    this.shareFamilyMemberIds = navParams.get('sharedFamilyMemberIds')
    this.getFamilyList();
0  }

  ionViewDidLoad() {
  }

  ionViewWillLeave() {
    window.localStorage.setItem('shareFamilyMember', JSON.stringify(this.shareFamilyMemberIds))
  }

  getFamilyList() {
    this.userAPISerivce.getBeneficiaryFamilyMember().then(res => {
      this.familyMemberList = res['list'];
    }).catch(error => {
      this.familyMemberList = [];
    })
  }

  shareInsuranceWithMember(id) {
    if (this.shareFamilyMemberIds.indexOf(id) == -1) {
      this.shareFamilyMemberIds.push(id);
    } else {
      this.shareFamilyMemberIds.splice(this.shareFamilyMemberIds.indexOf(id), 1);
    }
  }

  shareInsurance() {
    const shareInsuranceData = { policyId: this.policyId, familyMemberIds: this.shareFamilyMemberIds };
    this.insuranceAPI.shareInsurance(shareInsuranceData).then(res => {
      this.util.showAlert(res['msg']);
      window.localStorage.setItem('shareFamilyMember', JSON.stringify(this.shareFamilyMemberIds))
    }).catch(error => {
      let tmpError = error.error;
      this.util.showAlert(tmpError.error);
    })
  }
}



