import { Component, ViewChild } from '@angular/core';
import { NavParams, Tab, Tabs, Platform, Events } from 'ionic-angular';
import { Badge } from '@ionic-native/badge';

import { DashboardPage } from '../dashboard/dashboard';
import { NotificationsPage } from '../notifications/notifications';
import { SettingsPage } from '../settings/settings';
import { FaqPage } from '../faq/faq';

import { NotificationServiceProvider } from '../../providers/notification-service/notification-service'


@Component({
  templateUrl: 'tabs.html'
})
export class TabsPage {
  @ViewChild('myTabs') tabRef: Tabs;
  @ViewChild('settingTab') settingTab: Tab;
  @ViewChild('notificationTab') notificationTab: Tab;

  tab1Root = DashboardPage;
  tab2Root = SettingsPage;
  tab3Root = FaqPage;
  tab4Root = NotificationsPage;
  unReadCount = 0;
  constructor(private nav: NavParams, private platform: Platform, private notificationAPI: NotificationServiceProvider, private event: Events, private badge: Badge) {
    platform.ready().then(() => {
      if (nav.get('moveToSetting')) {
        setTimeout(() => {
          this.tabRef.select(this.settingTab);
        }, 500);
      }
      this.getNotifications();
      this.event.unsubscribe('updateNotifications');
      this.event.subscribe('updateNotifications', (res => {
        this.getNotifications();
      }));
      this.event.publish('checkDuplicateMail');
    }).catch(error => {
      console.log(error);
    })
  }

  getNotifications() {
    this.notificationAPI.getNotifications().then(res => {
      this.unReadCount = 0;
      let personalNotifications = res['personalNotifications']
      for (const notification of personalNotifications) {
        if (notification.status === 'Unread') {
          this.unReadCount += 1;
        }
        if (this.unReadCount === 0) {
          this.badge.clear();
          this.notificationTab.tabBadge = '';
        } else {
          if (this.platform.is('ios')) {
            this.badge.set(this.unReadCount);
          } else if (this.platform.is('android')) {
            this.badge.set(1);
          }
          this.event.publish('fetchNotificationData');
          this.notificationTab.tabBadge = '' + this.unReadCount;
        }
      }
    }).catch(error => {

    })

  }
}



