import { Component } from '@angular/core';
import { NavController, NavParams, Events } from 'ionic-angular';
import { AppVersion } from '@ionic-native/app-version';

import 'rxjs/add/operator/map'
import { FaqQuestionsPage } from '../../pages/faq-questions/faq-questions';
import { CommonServiceProvider } from '../../providers/common-service/common-service'

@Component({
  selector: 'page-faq',
  templateUrl: 'faq.html',
})
export class FaqPage {
  content: any[];
  faqData;

  categoryList = [];
  version = '1.0';
  constructor(public navCtrl: NavController, public navParams: NavParams, private commonAPIService: CommonServiceProvider, private event: Events, private appVersion: AppVersion) {
    this.appVersion.getVersionNumber().then(res => {
      this.version = res;
    });
  }

  ionViewWillEnter() {
    this.commonAPIService.getFAQ().then(res => {
      console.log(res);
      this.faqData = res['faq']['category'];
      this.categoryList = [];
      this.faqData.map(category => {
        this.categoryList.push(category);
      })
    }).catch(error => {
      console.log(error);
    });
    this.event.publish('trackView', 'On Faq list');
  }

  ionViewDidLoad() {
  }

  pushCategoryQuetions(category) {
    console.log('category', category);
    this.navCtrl.push(FaqQuestionsPage, { category: JSON.stringify(category) });
  }

}



