import { Component } from '@angular/core';
import { NavController, NavParams, Events, Platform } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Facebook, FacebookLoginResponse, } from '@ionic-native/facebook';
import { GooglePlus } from '@ionic-native/google-plus';
import { TermsScreenPage } from '../terms-screen/terms-screen';
import { UtilityProvider } from '../../providers/utility/utility'
import { UserServiceProvider } from '../../providers/user-service/user-service'
import { UserEmailServiceProvider } from '../../providers/user-email-service/user-email-service'
import { ValidationService } from '../../providers/validation-service/validation-service'
import { AppVersion } from '@ionic-native/app-version';

@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})

export class SignupPage {
  private userData: FormGroup;
  isPassword = true;
  status = false;
  iscPassword = true;
  cStatus = false;
  submitted = false;
  public mask = [0, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]
  version = '1.0';
  constructor(public navCtrl: NavController, public navParams: NavParams, private formBuilder: FormBuilder, private utilityService: UtilityProvider, private userAPIService: UserServiceProvider, private fb: Facebook, private googlePlus: GooglePlus, private event: Events, private userEmailService: UserEmailServiceProvider, private platform: Platform, private appVersion: AppVersion) {
    platform.ready().then(() => {
      this.appVersion.getVersionNumber().then(res => {
        this.version = res;
      });
    });
    this.userData = this.formBuilder.group({
      fName: [''],
      lName: [''],
      email: ['', Validators.compose([Validators.required, ValidationService.emailValidator])],
      number: [''],
      cPassword: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  ionViewWillEnter() {
    this.event.publish('trackView', 'On SignUp');
  }

  ionViewDidLoad() {
  }

  signIn() {
    this.navCtrl.pop();
  }

  signUp() {
    this.submitted = true;
    if (!this.userData.get('email').valid) {
      // this.utilityService.presentToast('ERROR_VALID_EMAIL', true);
    } else if (!this.userData.get('password').valid) {
      // this.utilityService.presentToast('ERROR_PASSWORD', true);
    } else if (this.userData.get('password').value.length < 6) {
      // this.utilityService.presentToast('ERROR_PASSWORD_LENGHT', true);
    } else if (!this.utilityService.hasLowerCase(this.userData.get('password').value)) {
      // this.utilityService.presentToast('ERROR_PASSWORD_SMALL', true);
    } else if (!this.utilityService.hasUpperCase(this.userData.get('password').value)) {
      // this.utilityService.presentToast('ERROR_PASSWORD_CAPITAL', true);
    } else if (!this.userData.get('cPassword').valid) {
      // this.utilityService.presentToast('ERROR_CONFIRM_PASSWORD', true);
    } else if (this.userData.get('cPassword').value != this.userData.get('password').value) {
      // this.utilityService.presentToast('ERROR_PASSWORD_CONFIRM_PASSWORD', true);
    } else {
      this.submitted = false;
      let userRequest = {
        "firstName": this.userData.get('fName').value ? this.userData.get('fName').value : undefined,
        "lastName": this.userData.get('lName').value ? this.userData.get('lName').value : undefined,
        "password": this.userData.get('password').value,
        "phone": this.userData.get('number').value ? this.userData.get('number').value : undefined,
        "email": this.userData.get('email').value,
        "loginType": 'Email'
      }
      this.userAPIService.registerUser(userRequest).then(res => {
        if (res['msg']) {
          this.utilityService.showAlert(res['msg']);
          this.navCtrl.pop();
        } else {
          this.userEmailService.isFromSignup = true
          this.event.publish('emailExists', { email: this.userData.get('email').value, userData: userRequest });
        }
      }).catch(error => {
        if (error['error']['error']) {
          this.utilityService.showAlert(error['error']['error']);
        }
      })
    }
  }



  facebook() {
    this.fb.logout().then(res => {

    }).catch(error => {

    })
    this.fb.login(['public_profile', 'email', 'user_friends']).then((success: FacebookLoginResponse) => {
      console.log(success);
      this.fb.api('me?fields=id,first_name,last_name,email,picture.width(720).height(720).as(picture_large)', ['public_profile']).then((profile_data) => {
        console.log('profile_data', profile_data);
        // let provider = 'facebook';
        const profile: any = {};
        profile.emails = [{ type: 'account', value: profile_data.email }];
        profile._json = profile_data;
        let profileData = {
          "lastName": profile_data['last_name'],
          "firstName": profile_data['first_name'],
          "profilePic": profile_data['picture_large']['data']['url'],
          "email": profile_data['email'],
          "id": profile_data['id']
        }

        let socialInfo = {
          "accessToken": success.authResponse.accessToken,
          "id": profile_data['id'],
          "profile": profileData
        }
        let userRequestData = {
          "provider": "Facebook",
          "lastName": profile_data['last_name'],
          "firstName": profile_data['first_name'],
          "profilePic": profile_data['picture_large']['data']['url'],
          "socialInfo": socialInfo,
          platform: this.platform.is('ios') ? 'iOS' : 'Android',
          deviceToken: window.localStorage.getItem('DeviceToken'),
          version: this.version
        }
        this.userAPIService.socialLoginUser(userRequestData).then(res => {
          console.log(res);
          if ('multiple' in res && res['multiple']) {
            this.userEmailService.isFromSignup = false
            window.localStorage.removeItem('noEmailFound');
            this.event.publish('emailExists', { email: profile_data['email'], userData: userRequestData });
          } else {
            if (profile_data['email']) {
              window.localStorage.removeItem('noEmailFound');
              this.event.publish('loggedIn');
            } else {
              this.event.publish('emailExists', { email: profile_data['email'], userData: userRequestData });
            }
          }
        }).catch(error => {
          let msg = error.error;
          console.log(error);
          if (msg.error) {
            this.utilityService.showAlert(msg.error);
          }
        })
      }, (error_profile) => {
        console.log(error_profile);
        // loading.dismissAll();
      });
    }).catch(e => { console.log('Error logging into Facebook', e); });
  }

  googleLogin() {
    this.googlePlus.logout().then(res => {

    }).catch(err => {

    })
    // 'webClientId': "268170607060-8ul2up810gap06oil5cjaku686pujqbo.apps.googleusercontent.com" Production
    // 'webClientId': "26192727788-jcvo0647356hg6uadj7i9r952epjebps.apps.googleusercontent.com" Staging
    this.googlePlus.login({
      'webClientId': "268170607060-8ul2up810gap06oil5cjaku686pujqbo.apps.googleusercontent.com"
    }).then((res) => {
      let profileData = {
        "lastName": res['familyName'],
        "firstName": res['givenName'],
        "profilePic": res['imageUrl'],
        "email": res['email'],
        "id": res['userId']
      }

      let socialInfo = {
        "accessToken": res['idToken'],
        "id": res['userId'],
        "profile": profileData
      }
      let userRequestData = {
        "provider": "Google",
        "lastName": res['familyName'],
        "firstName": res['givenName'],
        "profilePic": res['imageUrl'],
        "socialInfo": socialInfo,
        platform: this.platform.is('ios') ? 'iOS' : 'Android',
        deviceToken: window.localStorage.getItem('DeviceToken'),
        version: this.version
      }
      this.userAPIService.socialLoginUser(userRequestData).then(ress => {
        if ('multiple' in ress && ress['multiple']) {
          this.userEmailService.isFromSignup = false
          window.localStorage.removeItem('noEmailFound');
          this.event.publish('emailExists', { email: res['email'], userData: userRequestData });
        } else {
          window.localStorage.removeItem('noEmailFound');
          this.event.publish('loggedIn');
        }
      }).catch(error => {
        let msg = error.error;
        if (msg.error) {
          this.utilityService.showAlert(msg.error);
        }
      })
    }).catch((err) => {
      console.error("err", err);
    });
  }

  passwordType() {
    return this.isPassword ? 'password' : 'text';
  }

  showPassword = () => {
    this.isPassword = !this.isPassword;
    this.status = !this.status;
  }

  cPasswordType() {
    return this.iscPassword ? 'password' : 'text';
  }

  showcPassword = () => {
    this.iscPassword = !this.iscPassword;
    this.cStatus = !this.cStatus;
  }

  termsData(url) {
    this.navCtrl.push(TermsScreenPage, { url: url });
  }

  moveFocus(event, nextElement) {
    if (event.code == 'Enter') {
      nextElement.setFocus();
    }
  }
}



