import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ConfigServiceProvider } from '../config-service/config-service';
import { AuthServiceProvider } from '../auth-service/auth-service'
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class ApiServiceProvider {
  APP_URL = this.config.get('APP_URL');
  GOOGLE_KEY = this.config.get('GOOGLE_KEY')
  languageCode;
  constructor(public http: HttpClient, protected config: ConfigServiceProvider, private auth: AuthServiceProvider) {
  }

  get(URL) {
    let response = this.http.get(this.APP_URL + URL, {
      headers: new HttpHeaders().set('X-L10N-Locale', this.auth.langCode)
    }).map(res => res);
    return response

  }

  getWithHeader(URL) {
    let response = this.http.get(this.APP_URL + URL, {
      headers: this.createAuthorizationHeader()
    }).map(res => res);
    return response
  }

  postWithHeader(URL, data) {
    let response = this.http.post(this.APP_URL + URL, data, {
      headers: this.createAuthorizationHeader()
    }).map(res => res);
    return response
  }

  getBaseURL() {
    return this.APP_URL;
  }

  post(URL, data) {
    let response = this.http.post(this.APP_URL + URL, data, {
      headers: new HttpHeaders().set('X-L10N-Locale', this.auth.langCode)
    }).map(res => res);
    return response
  }
  
  createAuthorizationHeader() {
    let loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'));
    let headers = new HttpHeaders().set("authorization", 'Bearer ' + loggedUser['token']).set('X-L10N-Locale', this.auth.langCode);
    return headers
  }

  putImageToS3 = (url, data) => {
    const headers = new HttpHeaders().set('Content-Type', 'image/png');
    return this.http.put(url, data, { headers }).map(
      (response) => {
        return response;
      }, (error) => {
        return error;
      },
    );
  }


  putWithHeader(URL) {
    let response = this.http.put(this.APP_URL + URL, {
      headers: this.createAuthorizationHeader()
    }).map(res => res);
    return response
  }
  deleteWithHeader(URL, data) {
    let response = this.http.delete(this.APP_URL + URL, data).map(res => res);
    return response
  }

  geoCoding(URL) {
    let response = this.http.get(URL).map(res => res);
    return response
  }
}



