import { Component, Input } from '@angular/core';
import { LoadingController, Platform, ActionSheetController, Events, AlertController } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { UtilityProvider } from '../../../providers/utility/utility'
import { UserServiceProvider } from '../../../providers/user-service/user-service'
import { Sim } from '@ionic-native/sim';
import { TranslateService } from '@ngx-translate/core';


@Component({
  selector: 'family-detail',
  templateUrl: 'family-detail.html'
})
export class FamilyDetailComponent {
  @Input() isEdit = false;
  @Input() familyList;
  private familyDetail: FormGroup;
  isEditing = false;
  uploadPrevciew;
  showingPriviews = false;
  editFamilyMember;
  countryCode;
  submitted = false;
  relationShipList = [
    'RELATIONSHIP_HUSBAND',
    'RELATIONSHIP_WIFE',
    'RELATIONSHIP_FATHER',
    'RELATIONSHIP_MOTHER',
    'RELATIONSHIP_SON',
    'RELATIONSHIP_DAUGHTER',
    'RELATIONSHIP_FIL',
    'RELATIONSHIP_MIL',
    'RELATIONSHIP_SIL',
    'RELATIONSHIP_DIL',
    'RELATIONSHIP_GF',
    'RELATIONSHIP_GM',
    'RELATIONSHIP_EB',
    'RELATIONSHIP_YB',
    'RELATIONSHIP_ES',
    'RELATIONSHIP_YS',
    'RELATIONSHIP_GS',
    'RELATIONSHIP_GD',
    'RELATIONSHIP_GFIL',
    'RELATIONSHIP_GMIL',
    'RELATIONSHIP_EBIL',
    'RELATIONSHIP_YBIL',
    'RELATIONSHIP_ESIL',
    'RELATIONSHIP_YSIL',
    'RELATIONSHIP_GSIL',
    'RELATIONSHIP_GDIL',
    'RELATIONSHIP_OR'];
  constructor(public actionSheetCtrl: ActionSheetController,
    private formBuilder: FormBuilder,
    private utilityService: UtilityProvider,
    private camera: Camera,
    private platform: Platform,
    public actionsheetCtrl: ActionSheetController,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    public event: Events,
    public sim: Sim,
    public translate: TranslateService,
    private userAPIService: UserServiceProvider) {
    this.familyDetail = this.formBuilder.group({
      fName: ['', Validators.required],
      lName: ['', Validators.required],
      relationShip: ['', Validators.required],
      email: ['', Validators.required],
      phNumber: ['', Validators.required],
      fax: ['', Validators.required],
      profielPic: ['', Validators.required],
      isBeneficiary: ['false', Validators.required],
    });

    this.sim.getSimInfo().then((info) => {
      this.countryCode = info['countryCode'] == 'in' ? "+91" : info['countryCode'] == "" ? "+91" : "+81";
      if (this.countryCode == "") {
        this.countryCode = "+91";
      }
    }, (err) => {
      this.countryCode = "+91";
    });
    this.event.unsubscribe('resetFrom');
    this.event.subscribe('resetFrom', res => {
      this.submitted = false;
      this.familyDetail = this.formBuilder.group({
        fName: ['', Validators.required],
        lName: ['', Validators.required],
        relationShip: ['', Validators.required],
        email: ['', Validators.required],
        phNumber: ['', Validators.required],
        fax: ['', Validators.required],
        profielPic: ['', Validators.required],
        isBeneficiary: ['false', Validators.required],
      });
      this.uploadPrevciew = undefined;
      this.isEditing = false;
    });
  }


  editDetails(familyMember, index) {
    let actionSheetList = [{
      text: 'EDIT',
      role: 'destructive',
      handler: () => {
        this.editFamilyMember = familyMember;
        this.familyDetail = this.formBuilder.group({
          fName: [familyMember.firstName, Validators.required],
          lName: [familyMember.lastName, Validators.required],
          relationShip: [familyMember.relationship, Validators.required],
          email: [familyMember.email, Validators.required],
          phNumber: [familyMember.phone, Validators.required],
          fax: [familyMember.fax, Validators.required],
          profielPic: [familyMember.profilePic, Validators.required],
          isBeneficiary: [familyMember.beneficiary, Validators.required],
        });
        this.uploadPrevciew = familyMember.profilePic;
        this.event.publish('isEdit');
        this.isEdit = !this.isEdit;
        if (this.isEdit) {
          this.event.publish('trackView', 'On Family Edit');
        }
        this.isEditing = true
      }
    }, {
      text: 'REMOVE',
      handler: () => {
        this.presentConfirm(familyMember, index)
      }
    }, {
      text: 'CANCEL',
      role: 'cancel',
      handler: () => {
      },
    }];
    for (let action of actionSheetList) {
      this.translate.get(action['text']).subscribe(
        value => {
          action['text'] = value;
        })
    }
    let actionSheet = this.actionSheetCtrl.create({
      cssClass: 'select-action-sheet',
      buttons: actionSheetList
    });
    actionSheet.present();
  }

  presentConfirm(familyMember, index) {
    let alertList = [{
      text: 'YES',
      handler: () => {
        this.userAPIService.deleteFamilyMember({ id: familyMember._id }).then(res => {
          this.familyList.splice(index, 1);
        }).catch(error => {
          let tmpDic = error.error;
          if (tmpDic.msg) {
            this.utilityService.showAlert(tmpDic.msg)
          }
        })
      }
    }, {
      text: 'NO',
      handler: () => {
      }
    }]
    for (let alert of alertList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let title = 'iBeed Mari'
    this.translate.get('LOGO_NAME').subscribe(
      value => {
        title = value;
      })
    let message = 'Are you sure, you want to remove this family member?'
    this.translate.get('DELETE_FAMILY_CONFIRMATION').subscribe(
      value => {
        message = value;
      })
    let alert = this.alertCtrl.create({
      title: title,
      message: message,
      buttons: alertList
    });
    alert.present();
  }

  changeBeneficiary(familyMember) {
    let requestData = {
      beneficiary: !familyMember.beneficiary
    }
    requestData['familyMemberId'] = familyMember._id;
    this.userAPIService.editFamilyMember(requestData).then(res => {
      this.event.publish('updateFamilyList');
      this.utilityService.showAlert(res['msg']);
    }).catch(error => {
      this.utilityService.showAlert(error['error']['error']);
      // let tmpDic = error.error;
      this.event.publish('updateFamilyList');
      // this.utilityService.showAlert(tmpDic.msg);
    })
  }

  addFamilyMember() {
    console.log(this.familyDetail);
    this.submitted = true;
    if (!this.familyDetail.get('fName').valid) {
      // this.utilityService.presentToast('ERROR_FIRST_NAME', true);
    } else if (!this.familyDetail.get('lName').valid) {
      // this.utilityService.presentToast('ERROR_LAST_NAME', true);
    } else if (!this.familyDetail.get('relationShip').valid) {
      // this.utilityService.presentToast('ERROR_RELATIONSHIP', true);
    } else if (!this.familyDetail.get('email').valid) {
      // this.utilityService.presentToast('ERROR_EMAIL', true);
    } else if (!this.familyDetail.get('profielPic').valid) {
      // this.utilityService.presentToast('ERROR_PROFILE_PIC', true);
    }
    else {
      this.submitted = false;
      let familyMemberData = {
        firstName: this.familyDetail.get('fName').value,
        lastName: this.familyDetail.get('lName').value,
        // phone: this.familyDetail.get('phNumber').value,
        email: this.familyDetail.get('email').value,
        relationship: this.familyDetail.get('relationShip').value,
        // beneficiary: this.familyDetail.get('isBeneficiary').value,
      };
      // if (this.familyDetail.get('fax').value && this.familyDetail.get('fax').value != "") {
      //   familyMemberData['fax'] = this.familyDetail.get('fax').value;
      // }
      if (this.isEditing) {
        if (!this.uploadPrevciew.includes('https://')) {
          familyMemberData['image'] = this.familyDetail.get('profielPic').value;
        }
        else {
          delete familyMemberData['image'];
        }
        familyMemberData['familyMemberId'] = this.editFamilyMember._id;
        this.userAPIService.editFamilyMember(familyMemberData).then(res => {
          this.isEdit = !this.isEdit
          this.event.publish('updateFamilyList');
          this.event.publish('isEditFalse');
          this.familyDetail = this.formBuilder.group({
            fName: ['', Validators.required],
            lName: ['', Validators.required],
            relationShip: ['', Validators.required],
            email: ['', Validators.required],
            // phNumber: ['', Validators.required],
            // fax: ['', Validators.required],
            profielPic: ['', Validators.required],
            // isBeneficiary: ['false', Validators.required],
          });
          this.isEditing = false;
        }).catch(error => {
          this.utilityService.showAlert(error['error']['error']);
        })
      } else {
        familyMemberData['image'] = this.familyDetail.get('profielPic').value;
        this.userAPIService.addFamilyMember(familyMemberData).then(res => {
          this.isEdit = !this.isEdit
          this.event.publish('updateFamilyList');
          this.event.publish('isEditFalse');
          this.familyDetail = this.formBuilder.group({
            fName: ['', Validators.required],
            lName: ['', Validators.required],
            relationShip: ['', Validators.required],
            email: ['', Validators.required],
            // phNumber: ['', Validators.required],
            // fax: ['', Validators.required],
            profielPic: ['', Validators.required],
            // isBeneficiary: ['false', Validators.required],
          });
          this.uploadPrevciew = undefined;
        }).catch(error => {
          this.utilityService.showAlert(error['error']['error']);
        })
      }
    }
  }

  takePicture() {
    let buttonList = [{
      text: 'PHOTO_LIBRARY',
      handler: () => {
        const type = this.camera.PictureSourceType.PHOTOLIBRARY;
        this.selectPicture(type);
      },
    }, {
      text: 'PHOTO_CAMERA',
      handler: () => {
        const type = this.camera.PictureSourceType.CAMERA;
        this.selectPicture(type);
      },
    }, {
      text: 'CANCEL',
      role: 'cancel', // will always sort to be on the bottom
      icon: !this.platform.is('ios') ? 'close' : null,
      handler: () => {
        //  UtilityServiceProvider.log('Cancel clicked');
      },
    },];
    for (let alert of buttonList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let localizeTitle = 'PHOTO_SOURCE'
    this.translate.get(localizeTitle).subscribe(
      value => {
        localizeTitle = value;
      })

    const actionSheet = this.actionsheetCtrl.create({
      title: localizeTitle,
      cssClass: 'action-sheets-basic-page',
      buttons: buttonList,
    });
    actionSheet.present();
  }
  changeImage() {
    this.takePicture();
  }

  removeImage() {
    this.showingPriviews = false;
  }

  selectPicture(sourceType) {
    const options: CameraOptions = {
      mediaType: this.camera.MediaType.PICTURE, // PHOTOLIBRARY
      quality: 100,
      targetHeight: 200,
      targetWidth: 200,
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType,
      encodingType: this.camera.EncodingType.PNG,
      saveToPhotoAlbum: false,
    };
    let loader = this.loadingCtrl.create();
    loader.present();
    this.camera.getPicture(options).then((imagePath) => {
      this.showingPriviews = true;
      this.uploadPrevciew = 'data:image/jpeg;base64,' + imagePath;
      this.familyDetail.controls['profielPic'].setValue(this.uploadPrevciew);
      loader.dismissAll();
    }, (err) => {
      loader.dismissAll();
    });

  }

  moveFocus(event, nextElement) {
    if (event.code == 'Enter') {
      nextElement.setFocus();
    }
  }

  editForm() {
    this.isEdit = !this.isEdit;
    this.event.publish('isEdit');
    if (this.isEdit) {
      this.familyDetail = this.formBuilder.group({
        fName: ['', Validators.required],
        lName: ['', Validators.required],
        relationShip: ['', Validators.required],
        email: ['', Validators.required],
        phNumber: ['', Validators.required],
        fax: ['', Validators.required],
        profielPic: ['', Validators.required],
        isBeneficiary: ['false', Validators.required],
      });
      this.event.publish('trackView', 'On Family Edit');
    } else {
      this.event.publish('trackView', 'On Family Create');
    }
  }
}



