//Angular 4 Module Imports
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { IonicModule } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';


//Discover Component Imports
import { PersonalDetailComponent } from './personal-detail/personal-detail';
import { AddressDetailComponent } from './address-detail/address-detail';
import { FamilyDetailComponent } from './family-detail/family-detail';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';


export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    PersonalDetailComponent,
    AddressDetailComponent,
    FamilyDetailComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    IonicModule.forRoot(PersonalDetailComponent),
    IonicModule.forRoot(AddressDetailComponent),
    IonicModule.forRoot(FamilyDetailComponent),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    })
  ],
  exports: [
    PersonalDetailComponent,
    AddressDetailComponent,
    FamilyDetailComponent
  ],

})
export class SettingsModule { }



