import { Component, Input } from '@angular/core';

@Component({
  selector: 'dashboard-listing-detail',
  templateUrl: 'dashboard-listing-detail.html'
})
export class DashboardListingDetailComponent {
  @Input() insurance;
  showPasswordChange = false;
  @Input() isShared;
  constructor() {
  }

  getImageName(type) {
    if (type == 'Auto') {
      return 'assets/imgs/dash-icon-1.svg'
    } else if (type == 'Health') {
      return 'assets/imgs/dash-icon-2.svg'
    } else if (type == 'Life') {
      return 'assets/imgs/dash-icon-4.svg'
    } else if (type == 'Pet') {
      return 'assets/imgs/dash-icon-3.svg'
    } else if (type == 'Home') {
      return 'assets/imgs/dash-icon-5.svg'
    }
  }
}


