import { Component } from '@angular/core';
import { NavController, NavParams, Events } from 'ionic-angular';
import { UserServiceProvider } from '../../providers/user-service/user-service'
import { UtilityProvider } from '../../providers/utility/utility'
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { ValidationService } from '../../providers/validation-service/validation-service'

@Component({
  selector: 'page-forgot-password',
  templateUrl: 'forgot-password.html',
})
export class ForgotPasswordPage {
  private forgotPasswordForm: FormGroup;
  submitted = false;
  constructor(public navCtrl: NavController, public navParams: NavParams, private userAPIService: UserServiceProvider, private utility: UtilityProvider, private formBuilder: FormBuilder, private event: Events) {
    this.forgotPasswordForm = this.formBuilder.group({
      email: ['', Validators.compose([Validators.required, ValidationService.emailValidator])],
    });
  }

  ionViewWillEnter() {
    this.event.publish('trackView', 'On Forgot Password');
  }

  ionViewDidLoad() {
  }

  sendRecoveryLink() {
    this.submitted = true;
    if (!this.forgotPasswordForm.get('email').valid) {
      // this.utility.presentToast('ERROR_VALID_EMAIL', true);
    } else {
      this.submitted = false;
      this.userAPIService.forgotPassword(this.forgotPasswordForm.get('email').value).then(res => {
        this.utility.showAlert(res['msg']);
        this.navCtrl.pop();
      }).catch(error => {
        let tmpDic = error.error;
        console.log(tmpDic);

        this.utility.showAlert(tmpDic.error);
      });
    }
  }
}



