import { Component, ViewChild } from '@angular/core';
import { NavController, LoadingController, Platform, Events } from 'ionic-angular';

import { InsuranceServiceProvider } from '../../providers/insurance-service/insurance-service'
import { AuthServiceProvider } from '../../providers/auth-service/auth-service'
import { InsuranceManagementPage } from '../insurance-management/insurance-management';
import { InsuranceDetailPage } from '../insurance-detail/insurance-detail';

import { Slides } from 'ionic-angular';

@Component({
  selector: 'page-dashboard',
  templateUrl: 'dashboard.html',
})
export class DashboardPage {
  @ViewChild(Slides) slides: Slides;

  allInsuranceList = [];
  autoInsuranceList = [];
  lifeInsuranceList = [];
  healthInsuranceList = [];
  petInsuranceList = [];
  homeInsuranceList = [];
  selectedInsranceType = "ALL";
  public query: string = 'ALL';
  loggedUser
  constructor(public navCtrl: NavController, private insuranceAPIService: InsuranceServiceProvider, public loadingCtrl: LoadingController, public platform: Platform, private event: Events, private auth: AuthServiceProvider) {
    this.loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'));
  }

  ionViewWillEnter() {
    this.event.publish('trackView', 'On Dashboard')
    this.getPolicyList();
    document.body.classList.remove('keyboard-is-close');
  }

  insuranceScreen() {
    this.navCtrl.push(InsuranceManagementPage);
  }

  selectInsurance(insurance) {
    if (insurance.value == 'ALL') {
      this.slides.slideTo(0, 0);
    }
    if (insurance.value == 'AUTO') {
      this.slides.slideTo(1, 0);
    }
    if (insurance.value == 'LIFE') {
      this.slides.slideTo(2, 0);
    }
    if (insurance.value == 'HEALTH') {
      this.slides.slideTo(3, 0);
    }
    if (insurance.value == 'PET') {
      this.slides.slideTo(4, 0);
    }
    if (insurance.value == 'HOME') {
      this.slides.slideTo(5, 0);
    }
    this.selectedInsranceType = insurance.value;
  }

  getPolicyList() {
    let loader = this.loadingCtrl.create({});
    loader.present();
    this.insuranceAPIService.getInsuranceList().then(res => {
      let data = res['data'];
      this.allInsuranceList = data['docs'];
      this.fileterInsurance();
      loader.dismiss();
    }).catch(error => {
      loader.dismiss();
      console.error(error);
    })
  }

  fileterInsurance() {
    this.autoInsuranceList = [];
    this.lifeInsuranceList = [];
    this.healthInsuranceList = [];
    this.petInsuranceList = [];
    this.homeInsuranceList = [];
    this.allInsuranceList.map(insurance => {
      if (insurance.insuranceProduct.policyType.toLowerCase() == 'auto') {
        this.autoInsuranceList.push(insurance);
      }
      else if (insurance.insuranceProduct.policyType.toLowerCase() == 'life') {
        this.lifeInsuranceList.push(insurance);
      }
      else if (insurance.insuranceProduct.policyType.toLowerCase() == 'health') {
        this.healthInsuranceList.push(insurance);
      }
      else if (insurance.insuranceProduct.policyType.toLowerCase() == 'pet') {
        this.petInsuranceList.push(insurance);
      }
      else if (insurance.insuranceProduct.policyType.toLowerCase() == 'home') {
        this.homeInsuranceList.push(insurance);
      }
    })
  }

  getImageName(type) {
    if (type == 'Auto') {
      return 'assets/imgs/dash-icon-1.svg'
    } else if (type == 'Health') {
      return 'assets/imgs/dash-icon-2.svg'
    } else if (type == 'Life') {
      return 'assets/imgs/dash-icon-4.svg'
    } else if (type == 'Pet') {
      return 'assets/imgs/dash-icon-3.svg'
    }
    else if (type == 'Home') {
      return 'assets/imgs/dash-icon-5.svg'
    }
  }

  showDetail(insurance) {
    this.navCtrl.push(InsuranceDetailPage, { insurance: JSON.stringify(insurance) });
  }

  slideChanges($event) {
    console.log(this.slides._activeIndex);
    if (this.slides._activeIndex == 6 && this.slides.swipeDirection == 'next') {
      this.slides.slideTo(5, 0.5);
    } else {
    }
  }

  slideChanged(event) {
    if (this.slides._activeIndex == 6 && this.slides.swipeDirection == 'next') {
      this.slides.slideTo(5, 0.5);
    } else {
      if (this.slides._activeIndex == 0) {
        this.query = 'ALL';
      }
      if (this.slides._activeIndex == 1) {
        this.query = 'AUTO';
      }
      if (this.slides._activeIndex == 2) {
        this.query = 'LIFE';
      }
      if (this.slides._activeIndex == 3) {
        this.query = 'HEALTH';
      }
      if (this.slides._activeIndex == 4) {
        this.query = 'PET';
      }
      if (this.slides._activeIndex == 5) {
        this.query = 'HOME';
      }
      this.selectedInsranceType = this.query
    }
  }

  isShared(insurance) {
    let isPolicyShare = false;
    if (insurance.notificationSharedWith) {
      for (const notificationShare of insurance.notificationSharedWith) {
        isPolicyShare = this.loggedUser.userData._id == notificationShare.toUserId
        if (isPolicyShare) {
          break;
        }
      }
    }
    return isPolicyShare
  }
}



