import { Component } from '@angular/core';
import { NavController, NavParams, Events, Platform } from 'ionic-angular';

import { SignupPage } from '../signup/signup';
import { ForgotPasswordPage } from '../forgot-password/forgot-password';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { UtilityProvider } from '../../providers/utility/utility'
import { UserServiceProvider } from '../../providers/user-service/user-service'
import { UserEmailServiceProvider } from '../../providers/user-email-service/user-email-service'
import { ValidationService } from '../../providers/validation-service/validation-service'
import { Facebook, FacebookLoginResponse, } from '@ionic-native/facebook';
import { GooglePlus } from '@ionic-native/google-plus';
import { AppVersion } from '@ionic-native/app-version';

@Component({
  selector: 'page-signin',
  templateUrl: 'signin.html',
})
export class SigninPage {
  private userData: FormGroup;
  isPassword = true;
  status = false;
  submitted = false;
  version = '1.0';
  constructor(public navCtrl: NavController, public navParams: NavParams, private formBuilder: FormBuilder, private utilityService: UtilityProvider, private userAPIService: UserServiceProvider, private fb: Facebook, private googlePlus: GooglePlus, private event: Events, private platform: Platform, private appVersion: AppVersion, private userEmailService: UserEmailServiceProvider) {
    platform.ready().then(() => {
      this.appVersion.getVersionNumber().then(res => {
        console.log('res', res);
        this.version = res;
      });
    });
    this.userData = this.formBuilder.group({
      password: ['', Validators.required],
      email: ['', Validators.compose([Validators.required, ValidationService.emailValidator])],
    });
  }

  ionViewWillEnter() {
    this.event.publish('trackView', 'On Login');
  }

  ionViewDidLoad() {
  }

  signIn() {
    this.submitted = true;
    if (!this.userData.get('email').valid) {
      // this.utilityService.presentToast('ERROR_VALID_EMAIL', true);
    } else if (!this.userData.get('password').valid) {
      // this.utilityService.presentToast('ERROR_PASSWORD', true);
    }
    else {
      this.submitted = false;
      let userRequest = {
        password: this.userData.get('password').value,
        email: this.userData.get('email').value,
        loginType: 'Email',
        platform: this.platform.is('ios') ? 'iOS' : 'Android',
        deviceToken: window.localStorage.getItem('DeviceToken'),
        version: this.version
      }

      this.userAPIService.loggedInUser(userRequest).then(res => {
        console.log('res', res);
        let userData = res['userData'];
        if ('isWalkThrough' in userData) {
          if (userData['isWalkThrough']) {
            this.event.publish('walkThrough');
          } else {
            this.event.publish('loggedIn');
          }
        } else {
          this.event.publish('loggedIn');
        }
      }).catch(error => {
        let msg = error.error;
        if (msg.error) {
          this.utilityService.showAlert(msg.error);
        }
      })
    }
  }

  signUp() {
    this.navCtrl.push(SignupPage);
  }

  facebook() {
    this.fb.logout().then(res => {

    }).catch(error => {

    })
    this.fb.login(['public_profile', 'email']).then((success: FacebookLoginResponse) => {
      this.fb.api('me?fields=id,first_name,last_name,email,picture.width(720).height(720).as(picture_large)', ['public_profile']).then((profile_data) => {
        const profile: any = {};
        profile.emails = [{ type: 'account', value: profile_data.email }];
        profile._json = profile_data;
        let profileData = {
          "lastName": profile_data['last_name'],
          "firstName": profile_data['first_name'],
          "profilePic": profile_data['picture_large']['data']['url'],
          "email": profile_data['email'],
          "id": profile_data['id']
        }

        let socialInfo = {
          "accessToken": success.authResponse.accessToken,
          "id": profile_data['id'],
          "profile": profileData
        }
        let userRequestData = {
          "provider": "Facebook",
          "lastName": profile_data['last_name'],
          "firstName": profile_data['first_name'],
          "profilePic": profile_data['picture_large']['data']['url'],
          "socialInfo": socialInfo,
          platform: this.platform.is('ios') ? 'iOS' : 'Android',
          deviceToken: window.localStorage.getItem('DeviceToken'),
          version: this.version
        }
        this.userAPIService.socialLoginUser(userRequestData).then(res => {
          console.log('profile_data', profile_data);
          if ('multiple' in res && res['multiple']) {
            this.userEmailService.isFromSignup = profile_data['email'] ? false : true
            window.localStorage.removeItem('noEmailFound');
            this.event.publish('emailExists', { email: profile_data['email'], userData: userRequestData });
          } else {
            let userData = res['userData'];
            if (userData['email']) {
              window.localStorage.removeItem('noEmailFound');
              if ('isWalkThrough' in userData) {
                if (userData['isWalkThrough']) {
                  this.event.publish('walkThrough');
                } else {
                  this.event.publish('loggedIn');
                }
              } else {
                this.event.publish('loggedIn');
              }
            } else {
              window.localStorage.setItem('noEmailFound', 'true');
              // window.localStorage.removeItem('LoggedUser');
              this.userEmailService.isFromSignup = true;
              this.event.publish('emailExists', { email: profile_data['email'], userData: userRequestData });
            }

          }
        }).catch(error => {
          let msg = error.error;
          if (msg.error) {
            this.utilityService.showAlert(msg.error);
          }
        })
      }, (error_profile) => {
        // loading.dismissAll();
      });
    }).catch(e => { console.log('Error logging into Facebook', e); });
  }

  googleLogin() {
    this.googlePlus.logout().then(res => {

    }).catch(err => {

    })
    // 'webClientId': "268170607060-8ul2up810gap06oil5cjaku686pujqbo.apps.googleusercontent.com" Production
    // 'webClientId': "26192727788-jcvo0647356hg6uadj7i9r952epjebps.apps.googleusercontent.com" Staging
    this.googlePlus.login({
      'webClientId': "268170607060-8ul2up810gap06oil5cjaku686pujqbo.apps.googleusercontent.com"
    }).then((res) => {
      let profileData = {
        "lastName": res['familyName'],
        "firstName": res['givenName'],
        "profilePic": res['imageUrl'],
        "email": res['email'],
        "id": res['userId']
      }

      let socialInfo = {
        "accessToken": res['idToken'],
        "id": res['userId'],
        "profile": profileData
      }
      let userRequestData = {
        "provider": "Google",
        "lastName": res['familyName'],
        "firstName": res['givenName'],
        "profilePic": res['imageUrl'],
        "socialInfo": socialInfo,
        platform: this.platform.is('ios') ? 'iOS' : 'Android',
        deviceToken: window.localStorage.getItem('DeviceToken'),
        version: this.version
      }
      this.userAPIService.socialLoginUser(userRequestData).then(ress => {
        console.log('res', ress);
        if ('multiple' in ress && ress['multiple']) {
          this.userEmailService.isFromSignup = false
          window.localStorage.removeItem('noEmailFound');
          this.event.publish('emailExists', { email: res['email'], userData: userRequestData });
        } else {
          window.localStorage.removeItem('noEmailFound');
          let userData = ress['userData'];
          if ('isWalkThrough' in userData) {
            if (userData['isWalkThrough']) {
              this.event.publish('walkThrough');
            } else {
              this.event.publish('loggedIn');
            }
          } else {
            this.event.publish('loggedIn');
          }
        }
      }).catch(error => {
        let msg = error.error;
        if (msg.error) {
          this.utilityService.showAlert(msg.error);
        }
      })
    }).catch((err) => {
      console.error("err", err);
    });
  }

  passwordType() {
    return this.isPassword ? 'password' : 'text';
  }

  showPassword = () => {
    this.isPassword = !this.isPassword;
    this.status = !this.status;
  }

  forgotPassword() {
    this.navCtrl.push(ForgotPasswordPage);
  }

  moveFocus(event, nextElement) {
    if (event.code == 'Enter') {
      nextElement.setFocus();
    }
  }
}


