import { Component, ApplicationRef } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { NavController, NavParams, ActionSheetController, AlertController, Platform, Events } from 'ionic-angular';
import { CallNumber } from '@ionic-native/call-number';
import { EmailComposer } from '@ionic-native/email-composer';
import { FileTransferObject } from '@ionic-native/file-transfer';
import { File, DirectoryEntry } from '@ionic-native/file';
import { TranslateService } from '@ngx-translate/core';

import { InsuranceServiceProvider } from '../../providers/insurance-service/insurance-service'
import { AuthServiceProvider } from '../../providers/auth-service/auth-service'
import { UtilityProvider } from '../../providers/utility/utility'

import { InsuranceManagementPage } from '../insurance-management/insurance-management';
import { FamilySharePage } from '../family-share/family-share';


@Component({
  selector: 'page-insurance-detail',
  templateUrl: 'insurance-detail.html',
})
export class InsuranceDetailPage {
  insurance;
  insuranceDetails;

  isInsuranceProof = false;
  backToDetail = false;
  backToback = true;
  loggedUser;
  fileTransfer: FileTransferObject;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private insuranceAPIService: InsuranceServiceProvider,
    private auth: AuthServiceProvider,
    public actionSheetCtrl: ActionSheetController,
    private alertCtrl: AlertController,
    private translate: TranslateService,
    private emailComposer: EmailComposer,
    public file: File,
    protected platform: Platform,
    private util: UtilityProvider,
    private currencyPipe: CurrencyPipe,
    private event: Events,
    private callNumber: CallNumber,
    public ref: ApplicationRef) {
    if (navParams.get('insurance')) {
      this.insurance = JSON.parse(navParams.get('insurance'));
    } else {
      this.insurance = { _id: navParams.get('insuranceId') };
    }
    this.loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'));
    this.getInsuranceDetail();
  }

  ionViewWillEnter() {
    this.event.publish('trackView', 'On Insurance View');

  }

  displayInsuranceProof() {
    if (this.insuranceDetails['signedUrl']) {
      this.isInsuranceProof = true;
      this.backToDetail = true;
      this.backToback = false;
    }
    else {
      let docKey;
      let mainDocKey;
      this.insuranceDetails.documents.map(res => {
        res.thumbs.map(imageDic => {
          if (imageDic.name == 'policy_detail_thumb') {
            docKey = imageDic.key;
          }
        })
        if (docKey == undefined) {
          docKey = res.key;
        }
        mainDocKey = res.key;
      })
      this.insuranceAPIService.getSignedUrlForImage(docKey).then(res => {
        this.insuranceDetails['signedUrl'] = res['data']['preSignedUrl'];
        this.isInsuranceProof = true;
        this.backToDetail = true;
        this.backToback = false;
      }).catch(error => {
      })
      this.insuranceAPIService.getSignedUrlForImage(mainDocKey).then(res => {
        this.insuranceDetails['mainSignedUrl'] = res['data']['preSignedUrl'];
        this.isInsuranceProof = true;
        this.backToDetail = true;
        this.backToback = false;
      }).catch(error => {
      })
    }

  }

  backToInsuranceProof() {
    this.isInsuranceProof = false;
    this.backToDetail = false;
    this.backToback = true;
  }

  ionViewDidLoad() {
  }

  getInsuranceDetail() {
    window.localStorage.removeItem('shareFamilyMember');
    this.insuranceAPIService.getInsuranceDetail(this.insurance._id).then(res => {
      this.insuranceDetails = res['data'];
      this.insurance.isOwner = this.insuranceDetails.userId === this.loggedUser.userData._id;
      if (!('isSelfBeneficiary' in this.insuranceDetails)) {
        this.insuranceDetails['isSelfBeneficiary'] = false;
      }
    }).catch(error => {
      const tmpError = error.error;
      this.util.showAlertWithAction(tmpError.error, () => { this.navCtrl.pop() });
    })
  }


  editInsurance() {
    let docKey;
    this.insuranceDetails.documents.map(res => {
      res.thumbs.map(imageDic => {
        if (imageDic.name == 'policy_detail_thumb') {
          docKey = imageDic.key;
        }
      })
      if (docKey == undefined) {
        docKey = res.key;
      }
    })
    this.insuranceAPIService.getSignedUrlForImage(docKey).then(res => {
      this.insuranceDetails['signedUrl'] = res['data']['preSignedUrl'];
      this.navCtrl.push(InsuranceManagementPage, { editInsurance: JSON.stringify(this.insuranceDetails) });
    }).catch(error => {
    })
  }

  editDetails() {
    if (this.insuranceDetails) {
      let actionSheetList = [{
        text: 'INSURANCE_SHARE',
        handler: () => {
          this.shareInsurance();
        },
      }]
      let removeItem = {
        text: 'REMOVE',
        handler: () => {
          this.presentConfirm();
        }
      }
      let editItem = {
        text: 'EDIT',
        role: 'destructive',
        handler: () => {
          let docKey;
          this.insuranceDetails.documents.map(res => {
            res.thumbs.map(imageDic => {
              if (imageDic.name == 'policy_detail_thumb') {
                docKey = imageDic.key;
              }
            })
            if (docKey == undefined) {
              docKey = res.key;
            }
          })
          this.insuranceAPIService.getSignedUrlForImage(docKey).then(res => {
            this.insuranceDetails['signedUrl'] = res['data']['preSignedUrl'];
            this.navCtrl.push(InsuranceManagementPage, { editInsurance: JSON.stringify(this.insuranceDetails) });
          }).catch(error => {
          })
        }
      }
      let cancelItem = {
        text: 'CANCEL',
        role: 'cancel',
        handler: () => {
        },
      }
      if (this.insurance.isOwner) {
        actionSheetList.push(editItem);
        actionSheetList.push(removeItem);
      }
      actionSheetList.push(cancelItem);
      for (let action of actionSheetList) {
        this.translate.get(action['text']).subscribe(
          value => {
            action['text'] = value;
          })
      }
      let actionSheet = this.actionSheetCtrl.create({
        cssClass: 'select-action-sheet',
        buttons: actionSheetList
      });
      actionSheet.present();
    }
  }

  presentConfirm() {
    let alertList = [{
      text: 'YES',
      handler: () => {
        let data = {
          id: this.insuranceDetails._id
        }
        this.insuranceAPIService.removeInsurance(data).then(res => {
          this.navCtrl.popToRoot();
        }).catch(error => {
        })
      }
    }, {
      text: 'NO',
      handler: () => {
      }
    }]
    for (let alert of alertList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let title = 'iBeed Mari'
    this.translate.get('LOGO_NAME').subscribe(
      value => {
        title = value;
      })
    let message = 'Are you sure, you want to remove this insurance?'
    this.translate.get('DELETE_INSURANCE_CONFIRMATION').subscribe(
      value => {
        message = value;
      })
    let alert = this.alertCtrl.create({
      title: title,
      message: message,
      buttons: alertList
    });
    alert.present();
  }


  shareInsurance() {
    let translatedKeyList = ['COMPANY', 'PRODUCT', 'POLICY_HOLDER', 'INSURED', 'REGISTER', 'PREMIUM', 'PREMIUMFREQUENCY', 'START_DATE', 'END_DATE']
    let imageURL;
    this.insuranceDetails.documents.map(res => {
      console.log(res);
      res.thumbs.map(imageDic => {
        if (imageDic.name == 'email_template_thumb') {
          imageURL = imageDic.key;
        }
      })
      if (imageURL == undefined) {
        res.thumbs.map(imageDic => {
          if (imageDic.name == 'policy_detail_thumb') {
            imageURL = imageDic.key;
          }
        })
      }
      if (imageURL == undefined) {
        imageURL = res.key;
      }
    })
    let translatedList = [];
    for (let str of translatedKeyList) {
      this.translate.get(str).subscribe(
        value => {
          translatedList.push(value);
        })
    }
    let dates = new Date(this.insuranceDetails.startDate);
    let day = dates.getDate();
    let monthIndex = dates.getMonth();
    let year = dates.getFullYear();
    let tmpStartDate = year + (monthIndex >= 9 ? '/' : '/0') + (monthIndex + 1) + (day > 9 ? '/' : '/0') + day;
    dates = new Date(this.insuranceDetails.endDate);
    day = dates.getDate();
    monthIndex = dates.getMonth();
    year = dates.getFullYear();
    let tmpEndDate = year + (monthIndex >= 9 ? '/' : '/0') + (monthIndex + 1) + (day > 9 ? '/' : '/0') + day;
    this.fileTransfer = new FileTransferObject();
    this.insuranceAPIService.getSignedUrlForImage(imageURL).then(signedURL => {
      let loading = this.util.getLoader();
      loading.present();
      this.getPathForInternalDir('').then(res => {
        this.fileTransfer.onProgress((e) => {
          if (e) {
            let progress = (e.lengthComputable) ? Math.round(e.loaded / e.total * 100) : -1;
            loading.setContent("" + progress + " %");
            this.ref.tick();
          }
        });
        this.fileTransfer.download(signedURL['data']['preSignedUrl'], "" + res, true).then(entry => {
          let tableRow = (this.platform.is('android') ? "<br><br>" : "") + "<tr><td>"
          let registerName = this.loggedUser.userData.lastName + ' ' + this.loggedUser.userData.firstName;
          let companyNameStr = tableRow + translatedList[0] + " : </td><td>" + this.insuranceDetails.organization.name + "</td></tr>"
          let productNameStr = tableRow + translatedList[1] + " : </td><td>" + this.insuranceDetails.insuranceProduct.petName + "</td></tr>"
          let policyHolderStr = tableRow + translatedList[2] + " : </td><td>" + this.insuranceDetails.policyHolder + "</td></tr>"
          let insuredStr = tableRow + translatedList[3] + " : </td><td>" + this.insuranceDetails.insured + "</td></tr>"
          let registerStr;
          if (registerName != ' ') {
            registerStr = tableRow + translatedList[4] + " : </td><td>" + registerName + "</td></tr>"
          }
          let premiumAmtStr = tableRow + translatedList[5] + " : </td><td>" + this.currencyPipe.transform(this.insuranceDetails.premiumAmount, ' ') + "</td></tr>"
          let premiumPaymentStr = tableRow + translatedList[6] + " : </td><td>" + this.insuranceDetails.premiumPaymentFreq + "</td></tr>"
          let startDateStr = tableRow + translatedList[7] + " : </td><td>" + tmpStartDate + "</td></tr>"
          let endDateStr = tableRow + translatedList[8] + " : </td><td>" + tmpEndDate + "</td></tr>"
          let emailBodyStr = "<html><head><body><table>";
          emailBodyStr += companyNameStr;
          emailBodyStr += productNameStr;
          emailBodyStr += policyHolderStr;
          emailBodyStr += insuredStr;
          if (registerStr) {
            emailBodyStr += registerStr;
          }
          emailBodyStr += premiumAmtStr;
          emailBodyStr += premiumPaymentStr;
          emailBodyStr += startDateStr;
          emailBodyStr += endDateStr;
          emailBodyStr += "</table></body></head></html>";
          console.log('emailBodyStr', emailBodyStr);
          this.emailComposer.isAvailable().then((available: boolean) => {
            console.log('available', available);
            let email = {
              app: this.platform.is('android') ? 'gmail' : '',
              to: '',
              cc: '',
              bcc: [],
              attachments: [
                "" + res
              ],
              subject: '',
              body: emailBodyStr,
              isHtml: true
            };
            // Send a text message using default options
            this.emailComposer.addAlias('gmail', 'com.google.android.gm');
            this.emailComposer.open(email);
            loading.dismissAll();

          }).catch(error => {
            this.util.presentToast("MAIL_ERROR", true)
            console.log('error', error);
            loading.dismissAll();
          });
        }).catch(error => {
          loading.dismissAll();
          this.util.presentToast(error.body, false);
          console.log('Download error source', error.source);
          console.log('Download error body', error.body);
        });
      }).catch(error => {
        loading.dismissAll();
        console.log('FilePath error', error);
      })
    }).catch(error => {
    })
  }


  //Get path for episode download.
  getPathForInternalDir(feedId) {
    const currentObj = this;
    return new Promise((resolve, reject) => {
      let Dir = currentObj.file.documentsDirectory;
      if (currentObj.file.externalRootDirectory) {
        Dir = currentObj.file.externalRootDirectory;
      }
      let folderPath = '';
      if (currentObj.platform.is('android')) {
        currentObj.file.resolveLocalFilesystemUrl(Dir).then((fileSystem: DirectoryEntry) => {
          currentObj.file.getDirectory(fileSystem, '', { create: true, exclusive: true }).then((dirEntry: DirectoryEntry) => {
            currentObj.file.getDirectory(fileSystem, folderPath, { create: true, exclusive: true }).then((dirEntry: DirectoryEntry) => {
              currentObj.getFile(dirEntry, feedId).then(path => {
                resolve(path);
              }).catch(err => {
                console.log('getFileError', err);
                reject(err);
              });
            }).catch(err => {
              currentObj.file.createDir(Dir, folderPath, false).then((dirEntry: DirectoryEntry) => {
                currentObj.getFile(dirEntry, feedId).then(path => {
                  resolve(path);
                }).catch(err => {
                  console.log('getFileError catch', err);
                  reject(err);
                });
              }).catch(err => {
                if (err.code == 12) {
                  fileSystem.fullPath = fileSystem.fullPath + folderPath;
                  currentObj.getFile(fileSystem, feedId).then(path => {
                    resolve(path);
                  }).catch(err => {
                    console.log('getFileError if catch', err);
                    reject(err);
                  });
                }
                else {
                  console.log('getFileError else catch', err);
                  reject(err);
                }
              });
            });
          }).catch(err => {
            currentObj.file.createDir(Dir, folderPath, false).then((dirEntry: DirectoryEntry) => {
              currentObj.getFile(dirEntry, feedId).then(path => {
                resolve(path);
              }).catch(err => {
                console.log('getFileError catch', err);
                reject(err);
              });
            }).catch(err => {
              if (err.code == 12) {
                fileSystem.fullPath = fileSystem.fullPath + folderPath;
                currentObj.getFile(fileSystem, feedId).then(path => {
                  resolve(path);
                }).catch(err => {
                  console.log('getFileError if catch', err);
                  reject(err);
                });
              }
              else {
                console.log('getFileError else catch', err);
                reject(err);
              }
            });
          });
        }).catch(err => {
          console.log('getDirectory', err);
          reject(err);
        });
      }
      else {
        currentObj.file.resolveLocalFilesystemUrl(Dir).then((fileSystem: DirectoryEntry) => {
          currentObj.file.getDirectory(fileSystem, folderPath, { create: true, exclusive: true }).then((dirEntry: DirectoryEntry) => {
            currentObj.getFile(dirEntry, feedId).then(path => {
              resolve(path);
            }).catch(err => {
              console.log('getFileError', err);
              reject(err);
            });
          }).catch(err => {
            currentObj.file.createDir(Dir, folderPath, false).then((dirEntry: DirectoryEntry) => {
              currentObj.getFile(dirEntry, feedId).then(path => {
                resolve(path);
              }).catch(err => {
                console.log('getFileError catch', err);
                reject(err);
              });
            }).catch(err => {
              if (err.code == 12) {
                fileSystem.fullPath = fileSystem.fullPath + folderPath;
                currentObj.getFile(fileSystem, feedId).then(path => {
                  resolve(path);
                }).catch(err => {
                  console.log('getFileError if catch', err);
                  reject(err);
                });
              }
              else {
                console.log('getFileError else catch', err);
                reject(err);
              }
            });
          });
        }).catch(err => {
          console.log('getDirectory', err);
          reject(err);
        });
      }
    });
  }

  getFile(dirEntry, feedId) {
    const currentObj = this;
    return new Promise((resolve, reject) => {
      currentObj.file.getFile(dirEntry, "image.png", { create: true, exclusive: false }).then(fe => {
        const p = fe.toURL();
        resolve(p);
      }).catch(err => {
        reject(err);
      });
    });
  }

  callHotLineNo() {
    this.callNumber.callNumber(this.insuranceDetails.hotlinePhoneNo, true).then(res => {
      console.log('Launched dialer!', res)
    }).catch(err => {
      console.log('Error launching dialer', err)
    });
  }

  shareDetails() {
    let ids = []
    let shareFamilyMember = JSON.parse(window.localStorage.getItem('shareFamilyMember'))
    console.log('shareFamilyMember', shareFamilyMember);
    if (shareFamilyMember) {
      ids = shareFamilyMember;
      window.localStorage.removeItem('shareFamilyMember');
    } else {
      if (this.insurance.notificationSharedWith && this.insurance.notificationSharedWith.length > 0) {
        ids = this.insurance.notificationSharedWith.map(member => member._id)
      }
    }
    this.navCtrl.push(FamilySharePage, { id: this.insurance._id, sharedFamilyMemberIds: ids })
  }
}



