import { Injectable } from '@angular/core';
import { LoadingController } from 'ionic-angular';
import { ApiServiceProvider } from '../api-service/api-service';
import { AuthServiceProvider } from '../auth-service/auth-service';

@Injectable()
export class InsuranceServiceProvider {

  constructor(public apiService: ApiServiceProvider, private auth: AuthServiceProvider, private loadingCtrl: LoadingController) {
  }


  getOrganisationList(type) {
    return new Promise((resolve, reject) => {
      this.apiService.get('organization/list-by-policy-type/' + type).subscribe(res => {
        resolve({ data: res['organizationList'] });
      }, err => {
        reject(err);
      });
    });
  }

  getProductList(companyId, category) {
    return new Promise((resolve, reject) => {
      this.apiService.get('insuranceproduct/org-product-type/' + companyId + '/' + category).subscribe(res => {
        resolve({ data: res['productList'] });
      }, err => {
        reject(err);
      });
    });
  }

  createInsurance(insuranceReqeustData) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('policy/create', insuranceReqeustData).subscribe(res => {
        resolve({ data: res });
      }, err => {
        reject(err);
      });
    });
  }

  editInsurance(insuranceReqeustData) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('policy/update', insuranceReqeustData).subscribe(res => {
        resolve({ data: res });
      }, err => {
        reject(err);
      });
    });
  }


  getInsuranceList() {
    return new Promise((resolve, reject) => {
      this.apiService.getWithHeader('policy/myPolicyList').subscribe(res => {
        let loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'));
        let count = res['policyList']['docs'].length;
        loggedUser['insuranceCount'] = count;
        this.auth.localStore("LoggedUser", JSON.stringify(loggedUser))
        resolve({ data: res['policyList'] });
      }, err => {
        reject(err);
      });
    });
  }

  getInsuranceDetail(insuranceId) {
    return new Promise((resolve, reject) => {
      this.apiService.getWithHeader('policy/policy-detail/' + insuranceId).subscribe(res => {
        resolve({ data: res['policyDetail'] });
      }, err => {
        reject(err);
      });
    });
  }

  getSignedUrlForImage(policyDoc: String) {
    return new Promise((resolve, reject) => {
      let loading = this.loadingCtrl.create({
      });
      loading.present();
      this.apiService.getWithHeader('user/get-doc-authenticate-url/' + policyDoc.replace("/", "%2f").replace("/", "%2f")).subscribe(res => {
        resolve({ data: res['document'] });
        loading.dismiss();
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }


  getSignedUrlForImageUpload(data) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('policy/policy-doc-presigned-url', data).subscribe(res => {
        resolve({ data: res['policyDetail'] });
      }, err => {
        reject(err);
      });
    });
  }


  putImageToS3(url, data) {
    return new Promise((resolve, reject) => {
      this.apiService.putImageToS3(url, data).subscribe(res => {
        resolve({ data: res });
      }, err => {
        reject(err);
      });
    });
  }

  removeInsurance(data) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('policy/remove-policy', data).subscribe(res => {
        resolve({ data: res['policyDetail'] });
      }, err => {
        reject(err);
      });
    });
  }

  shareInsurance(data) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('policy/share-policy', data).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  getBaseURL() {
    return this.apiService.getBaseURL();
  }
}



