import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { UserServiceProvider } from '../../providers/user-service/user-service'

@Component({
  selector: 'page-share-policy',
  templateUrl: 'share-policy.html',
})
export class SharePolicyPage {
  searchString
  currentPageSelected: number = 1;
  paginationValues = {
    total: 0,
    max: 0,
    pages: 0
  };
  users;
  selectedUsers;
  constructor(public navCtrl: NavController, public navParams: NavParams, private userAPI: UserServiceProvider) {
    this.selectedUsers = JSON.parse(window.localStorage.getItem('selectedUsers'));
  }

  ionViewDidLoad() {
  }

  serachUser(event) {
    if (this.searchString !== '') {
      this.userAPI.searchUser(this.currentPageSelected, JSON.stringify({ searchString: this.searchString })).then(res => {
        let userList = res['userList'];
        this.users = userList['organizationList']
        for (let user of this.users) {
          if (this.selectedUsers) {
            for (let selecteUser of this.selectedUsers) {
              user.isSelected = false;
              if (user._id === selecteUser._id) {
                user.isSelected = true;
                break;
              }
            }
          } else {
            user.isSelected = false;
          }
        }
        let tmpPagination = userList['paginationDetail'];
        if (tmpPagination['page'] == 1) {
          this.paginationValues = userList['paginationDetail'];
        }
        if (typeof event !== 'string') {
          event.complete();
        }
      }).catch(error => {
      })
    }
  }
  canceSearch() {
    this.searchString = '';
  }

  searchNextPage(event) {
    if (this.currentPageSelected < this.paginationValues['pages']) {
      this.currentPageSelected += 1;
      this.serachUser(event);
    } else {
      event.complete();
    }
  }

  selectUser(user) {
    console.log('user', user);
    user.isSelected = !user.isSelected;
  }

  saveSharedPerson() {
    this.selectedUsers = [];
    for (let user of this.users) {
      console.log(user.isSelected);
      if (user.isSelected) {
        this.selectedUsers.push(user);
      }
    }
    window.localStorage.setItem('selectedUsers', JSON.stringify(this.selectedUsers));
    this.navCtrl.pop();
  }
}



