import { Injectable } from '@angular/core';
import { FirebaseAnalytics } from '@ionic-native/firebase-analytics';

@Injectable()
export class FirebaseAnalyticsProvider {

  constructor(private firebaseAnalytics: FirebaseAnalytics) {
    if (this.firebaseAnalytics) {
      this.firebaseAnalytics.setEnabled(true);
    }
  }

  logPageViewEvent(pageName) {
    this.firebaseAnalytics.logEvent('page_view', { page: pageName }).then((res: any) => {
      console.log('res', res)
    }).catch((error: any) => {
      console.error('error', error)
    });
  }

  setScreen(pageName) {
    this.firebaseAnalytics.setCurrentScreen(pageName).then(res => {
      console.log('res', res)
    }).catch(error => {
      console.error('error', error)
    })
  }

}



