import { Injectable } from '@angular/core';
import { LoadingController } from 'ionic-angular';
import { ApiServiceProvider } from '../api-service/api-service';
import { AuthServiceProvider } from '../auth-service/auth-service'

@Injectable()
export class UserServiceProvider {

  constructor(public apiService: ApiServiceProvider, private auth: AuthServiceProvider, protected loadingCtrl: LoadingController) {
  }

  registerUser(userData) {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.post('user/signup', userData).subscribe(res => {
        loading.dismiss();
        resolve(res);
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  loggedInUser(userData) {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.post('user/authenticate', userData).subscribe(res => {
        let loggedUser = res
        this.auth.localStore("LoggedUser", JSON.stringify(loggedUser))
        this.auth.localStore("isUserLogged", true)
        loading.dismiss();
        resolve(res);
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  socialLoginUser(userData) {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.post('user/authenticate-provider', userData).subscribe(res => {
        if (!('multiple' in res)) {
          this.auth.localStore("isUserLogged", true)
        }
        let loggedUser = res
        this.auth.localStore("LoggedUser", JSON.stringify(loggedUser))
        loading.dismiss();
        resolve(res);
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  getFamilyMember() {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.getWithHeader('familyMember/my-family-member').subscribe(res => {
        loading.dismiss();
        resolve({ list: res['familyList'] });
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  deleteUser() {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('user/delete-user-account', {}).subscribe(res => {
        loading.dismiss();
        resolve(res)
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  addFamilyMember(familyMemberData) {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('familyMember/add', familyMemberData).subscribe(res => {
        loading.dismiss();
        resolve(res);
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  getBeneficiaryFamilyMember() {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.getWithHeader('familyMember/my-family-with-beneficiary').subscribe(res => {
        loading.dismiss();
        resolve({ list: res['familyList'] });
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  getProfile() {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.getWithHeader('user/profile').subscribe(res => {
        loading.dismiss();
        let loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'));
        loggedUser['userData'] = res
        this.auth.localStore("LoggedUser", JSON.stringify(loggedUser))
        this.auth.localStore("isUserLogged", true)
        resolve({ user: res });
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  editFamilyMember(familyMemberData) {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('familyMember/update', familyMemberData).subscribe(res => {
        loading.dismiss();
        resolve(res);
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  updateProfile(profileData) {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('user/update-profile', profileData).subscribe(res => {
        let loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'))
        if ('userData' in res) {
          loggedUser.userData = res['userData']
        }
        else if ('address' in res) {
          loggedUser.userData.address = res['address']
        }
        this.auth.localStore("LoggedUser", JSON.stringify(loggedUser))
        loading.dismiss();
        resolve(res);
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  deleteFamilyMember(data) {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('familyMember/remove-family-member', data).subscribe(res => {
        loading.dismiss();
        resolve(res);
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  changePassword(data) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('user/change-password', data).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  getAddress(address) {
    return new Promise((resolve, reject) => {
      this.apiService.geoCoding("https://maps.googleapis.com/maps/api/geocode/json?components=country:JP|postal_code:" + address.trim() + "&key=" + this.apiService.GOOGLE_KEY + "&sensor=false").subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  forgotPassword(email) {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.post('user/forgot-password', { 'email': email }).subscribe(res => {
        loading.dismiss();
        resolve(res);
      }, err => {
        loading.dismiss();
        reject(err);
      });
    });
  }

  updateLastActiveTime() {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('user/update-profile', {}).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  searchUser(page, searchString?) {
    return new Promise((resolve, reject) => {
      let query = "?" + ('page=' + page);
      if (searchString) {
        query = query + "&where=" + searchString
      }
      this.apiService.getWithHeader('user/user-list-for-share-policy' + query).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  logOut() {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('user/logout', {}).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  mergerAccount(userData) {
    return new Promise((resolve, reject) => {
      this.apiService.post('user/merge-request', userData).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  emailExists(userData) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('user/check-email', userData).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }
  migrationVerifed(userData) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('user/check-migration', userData).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  migrationFinished() {
    return new Promise((resolve, reject) => {
      this.apiService.getWithHeader('user/check-migration-status').subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  updateDeviceToken(userData) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('user/update-device-token', userData).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }
}




