import { Injectable } from '@angular/core';
import { ENV } from '@app/env'

@Injectable()
export class ConfigServiceProvider {
  protected config;
  constructor() {
    this.config = ENV;
  }

  get(key) {
    return this.config[key];
  }

  getConfigValue(key) {
    return this.config[key];
  }
}



