import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { Sim } from '@ionic-native/sim';
import { CallNumber } from '@ionic-native/call-number';

//Page imports
import { TabsPage } from '../pages/tabs/tabs';
import { SigninPage } from '../pages/signin/signin';
import { SignupPage } from '../pages/signup/signup';
import { DashboardPage } from '../pages/dashboard/dashboard';
import { ManagementPage } from '../pages/management/management';
import { SettingsPage } from '../pages/settings/settings';
import { FaqPage } from '../pages/faq/faq';
import { FaqQuestionsPage } from '../pages/faq-questions/faq-questions';
import { FaqDetailsPage } from '../pages/faq-details/faq-details';
import { InsuranceManagementPage } from '../pages/insurance-management/insurance-management';
import { InsuranceDetailPage } from '../pages/insurance-detail/insurance-detail';
import { BeneficiaryPage } from '../pages/beneficiary/beneficiary';
import { ForgotPasswordPage } from '../pages/forgot-password/forgot-password';
import { TermsScreenPage } from '../pages/terms-screen/terms-screen';
import { SharePolicyPage } from '../pages/share-policy/share-policy';
import { WalkthroughPage } from '../pages/walkthrough/walkthrough';
import { NotificationsPage } from '../pages/notifications/notifications';
import { FamilySharePage } from '../pages/family-share/family-share';


//Plugin Imports
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { SearchPipe } from '../pipes/search/search';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { Camera } from '@ionic-native/camera';
import { FileChooser } from '@ionic-native/file-chooser';
import { SelectSearchableModule } from 'ionic-select-searchable';
import { HttpClientModule } from '@angular/common/http';
import { DatePipe, CurrencyPipe } from '@angular/common'
import { Facebook } from '@ionic-native/facebook';
import { GooglePlus } from '@ionic-native/google-plus';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { HttpClient } from '@angular/common/http';
import { DatePicker } from '@ionic-native/date-picker';
import { AppVersion } from '@ionic-native/app-version';
import { Push } from '@ionic-native/push';
import { EmailComposer } from '@ionic-native/email-composer';
import { Badge } from '@ionic-native/badge';
import { Keyboard } from '@ionic-native/keyboard';
import { IonicImageViewerModule } from 'ionic-img-viewer';
import { FirebaseAnalytics } from '@ionic-native/firebase-analytics';
import { IonTagsInputModule } from "ionic-tags-input";

//Provider Imports
import { UtilityProvider } from '../providers/utility/utility';
import { ApiServiceProvider } from '../providers/api-service/api-service';
import { ConfigServiceProvider } from '../providers/config-service/config-service';
import { InsuranceServiceProvider } from '../providers/insurance-service/insurance-service';
import { UserServiceProvider } from '../providers/user-service/user-service';
import { AuthServiceProvider } from '../providers/auth-service/auth-service';
import { ValidationService } from '../providers/validation-service/validation-service';
import { CommonServiceProvider } from '../providers/common-service/common-service';
import { FirebaseAnalyticsProvider } from '../providers/firebase-analytics/firebase-analytics'
import { NotificationServiceProvider } from '../providers/notification-service/notification-service';
import { UserEmailServiceProvider } from '../providers/user-email-service/user-email-service';

//Component imports
import { SettingsModule } from '../components/Settings/settings.module'
import { DashboardModule } from '../components/Dashboard/dashboard.module'

//Third party
import { TextMaskModule } from 'angular2-text-mask';
import { HttpModule } from '@angular/http';
import createNumberMask from 'text-mask-addons/dist/createNumberMask'

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    MyApp,
    TabsPage,
    SigninPage,
    SignupPage,
    DashboardPage,
    ManagementPage,
    SettingsPage,
    FaqPage,
    InsuranceManagementPage,
    SearchPipe,
    InsuranceDetailPage,
    BeneficiaryPage,
    ForgotPasswordPage,
    TermsScreenPage,
    FaqQuestionsPage,
    FaqDetailsPage,
    SharePolicyPage,
    WalkthroughPage,
    NotificationsPage,
    FamilySharePage
  ],
  imports: [
    HttpModule,
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp),
    SelectSearchableModule,
    SettingsModule,
    DashboardModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    TextMaskModule,
    IonicImageViewerModule,
    IonTagsInputModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    TabsPage,
    SigninPage,
    SignupPage,
    DashboardPage,
    ManagementPage,
    SettingsPage,
    FaqPage,
    InsuranceManagementPage,
    InsuranceDetailPage,
    BeneficiaryPage,
    ForgotPasswordPage,
    TermsScreenPage,
    FaqQuestionsPage,
    FaqDetailsPage,
    SharePolicyPage,
    WalkthroughPage,
    NotificationsPage,
    FamilySharePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    FileTransfer,
    FileTransferObject,
    File,
    Camera,
    FileChooser,
    UtilityProvider,
    ApiServiceProvider,
    ConfigServiceProvider,
    InsuranceServiceProvider,
    DatePipe,
    UserServiceProvider,
    UserServiceProvider,
    AuthServiceProvider,
    ValidationService,
    Facebook,
    GooglePlus,
    Sim,
    CommonServiceProvider,
    DatePicker,
    createNumberMask,
    CurrencyPipe,
    EmailComposer,
    Keyboard,
    FirebaseAnalytics,
    FirebaseAnalyticsProvider,
    CallNumber,
    AppVersion,
    Push,
    NotificationServiceProvider,
    Badge,
    UserEmailServiceProvider
  ]
})
export class AppModule { }


