import { Component } from '@angular/core';
import { NavController, NavParams, Events } from 'ionic-angular';

@Component({
  selector: 'page-faq-details',
  templateUrl: 'faq-details.html',
})
export class FaqDetailsPage {
  questionData;
  constructor(public navCtrl: NavController, public navParams: NavParams, private event: Events) {
    this.questionData = JSON.parse(navParams.get('question'));
    console.log('sdasdad', this.questionData);

  }

  ionViewWillEnter() {
    this.event.publish('trackView', 'On Faq Details');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FaqDetailsPage');
  }

}



