import { Injectable } from '@angular/core';
import { LoadingController } from 'ionic-angular';
import { ApiServiceProvider } from '../api-service/api-service';

@Injectable()
export class CommonServiceProvider {

  constructor(public apiService: ApiServiceProvider, protected loadingCtrl: LoadingController) {
  }

  privacyScreen(url) {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.get(url).subscribe(res => {
        console.log(res);
        loading.dismiss();
        resolve(res);
      }, err => {
        console.log(err);
        loading.dismiss();
        reject(err);
      });
    });
  }

  getFAQ() {
    let loading = this.loadingCtrl.create({
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.apiService.get('static/faq').subscribe(res => {
        loading.dismiss();
        resolve(res);
      }, err => {
        console.log(err);
        loading.dismiss();
        reject(err);
      });
    });
  }

}




