import { Injectable } from '@angular/core';
import { LoadingController, AlertController, Events, Alert } from 'ionic-angular';
import { UserServiceProvider } from '../user-service/user-service';
import { AuthServiceProvider } from '../auth-service/auth-service';
import { UtilityProvider } from '../utility/utility';
import { TranslateService } from '@ngx-translate/core';

@Injectable()
export class UserEmailServiceProvider {
  socialUser
  isFromSignup = false;
  emailAlert: Alert;
  constructor(public userAPI: UserServiceProvider, protected loadingCtrl: LoadingController, private alertCtrl: AlertController, private translate: TranslateService, private util: UtilityProvider, private event: Events, private auth: AuthServiceProvider) {
  }

  emailForSocial(email) {
    const currentObj = this;
    let alertList = [{
      text: 'CANCEL',
      handler: () => {
        if (window.localStorage.getItem('noEmailFound') == 'true') {
          window.localStorage.removeItem('noEmailFound')
          window.localStorage.removeItem('LoggedUser')
          window.localStorage.removeItem('isUserLogged')
          this.event.publish('logout');
        }
      }
    }, {
      text: 'EMAIL_REGISTER',
      handler: (data) => {
        console.log('data', data);
        if (data.email != '') {
          const regexp = new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
          const serchfind = regexp.test(data.email);
          if (serchfind) {
            this.checkEmailExists(data, email);
          } else {
            this.util.presentToast('ERROR_VALID_EMAIL', true);
            this.emailForSocial('');
          }
        } else {
          this.util.presentToast('ERROR_VALID_EMAIL', true);
          this.emailForSocial('');
        }
      }
    }]
    for (let alert of alertList) {
      currentObj.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let message = 'iBeed Mari'
    currentObj.translate.get('EMAIL_MISSING').subscribe(
      value => {
        message = value;
      })

    let emails = "EMAIL"
    currentObj.translate.get(emails).subscribe(
      value => {
        emails = value;
      })
    this.emailAlert = currentObj.alertCtrl.create({
      message: message, inputs: [
        {
          name: 'email',
          placeholder: emails,
          value: email,
          type: 'email'
        }
      ],
      buttons: alertList,
      enableBackdropDismiss: false
    });
    this.emailAlert.present();
  }

  closeEmailAlert() {
    if (this.emailAlert) {
      this.emailAlert.dismiss();
    }
  }

  checkEmailExists(data, email) {
    this.userAPI.emailExists({ email: data.email.toLowerCase() }).then(res => {
      if (res['emailExist']) {
        if (this.socialUser.userData) {
          this.socialUser.userData.provider.profile['email'] = data.email.toLowerCase();
        } else {
          this.socialUser.socialInfo.profile['email'] = data.email.toLowerCase();
        }
        this.existingEmailAlert(data.email, email != '')
      } else {
        delete res['emailExist'];
        const loggedUserData = res;
        window.localStorage.setItem('LoggedUser', JSON.stringify(loggedUserData));
        this.translate.get('EMAIL_REGISTERED').subscribe(value => {
          this.util.showAlert(value)
        })
        if (this.isFromSignup) {
          if ('isWalkThrough' in loggedUserData['userData']) {
            if (loggedUserData['userData']['isWalkThrough']) {
              this.event.publish('walkThrough');
            } else {
              this.event.publish('loggedIn');
            }
          } else {
            this.event.publish('loggedIn');
          }
        }
      }
    }).catch(error => {

    })
  }

  existingEmailAlert(email, isExist) {
    const currentObj = this;
    let other = {
      text: 'EMAIL_USE_OTHER',
      handler: () => {
        if (!this.isFromSignup || this.socialUser.socialInfo) {
          currentObj.emailForSocial('');
        }
      }
    }
    let cancel = {
      text: 'CANCEL',
      handler: () => {
        if (window.localStorage.getItem('noEmailFound') == 'true') {
          window.localStorage.removeItem('noEmailFound')
          window.localStorage.removeItem('LoggedUser')
          window.localStorage.removeItem('isUserLogged')
          this.event.publish('logout');
        }
      }
    }
    let register = {
      text: 'MERGE_ACCOUNT',
      handler: () => {
        currentObj.sendMigrationLink();
      }
    }
    let alertList = []
    if (isExist && !this.isFromSignup) {
      alertList.push(cancel)
    } else {
      alertList.push(other)
    }
    alertList.push(register)
    for (let alert of alertList) {
      currentObj.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let message = 'iBeed Mari'
    currentObj.translate.get(this.isFromSignup ? 'EMAIL_EXISTS_TITLE_SIGNUP' : 'EMAIL_EXISTS_TITLE').subscribe(
      value => {
        message = value.replace('USER_EMAIL', email);
      })

    let alert = currentObj.alertCtrl.create({
      message: message,
      buttons: alertList,
      enableBackdropDismiss: false
    });
    alert.present();
  }

  sendMigrationLink() {
    console.log('mergerAccount', this.socialUser);
    this.userAPI.mergerAccount(this.socialUser).then(res => {
      this.translate.get('MIGRATION_LINK').subscribe(value => {
        this.util.showAlert(value)
      })
    }).catch(error => {

    })
  }

  checkMigrationLink(loggedUser) {
    this.socialUser = loggedUser;
    if (loggedUser.userData.provider) {
      this.isFromSignup = false;
      if (loggedUser.userData.provider.profile.email && loggedUser.userData.provider.profile.email != '') {
        this.userAPI.migrationVerifed({ email: loggedUser.userData.provider.profile.email }).then(res => {
          if (!res['migrationFinished']) {
            this.existingEmailAlert(loggedUser.userData.provider.profile.email, true);
          }
        }).catch(error => {

        })
      } else {
        console.log('loggedUser.email', loggedUser.email);
        if (loggedUser.userData.email == undefined) {
          this.emailForSocial(loggedUser.userData.provider.profile.email);
        }
      }
    }
  }
}




