import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { CommonServiceProvider } from '../../providers/common-service/common-service'

@Component({
  selector: 'page-terms-screen',
  templateUrl: 'terms-screen.html',
})
export class TermsScreenPage {
  privacyData = [];
  resHtmlData: any = null;
  URLName: String;
  title;
  header;
  constructor(public navCtrl: NavController, public navParams: NavParams, private commonServiceProvider: CommonServiceProvider) {
    this.URLName = this.navParams.get('url');
    console.log(this.URLName);
    let titles = this.URLName.split('/');
    this.title = titles[1];
    if (this.title == 'terms-of-use') {
      this.header = "Terms Screen";
    } else if (this.title == 'security-policy') {
      this.header = "Security Policy";
    } else if (this.title == 'privacy-policy') {
      this.header = "Privacy Policy";
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TermsScreenPage');
  }

  ionViewWillEnter() {

    this.commonServiceProvider.privacyScreen(this.URLName).then(res => {
      console.log("res", res);
      this.resHtmlData = res['content'];
    }).catch(error => {
      // this.utilityService.presentToast(error['error']['error'], false);
      console.log("error", error);
    })
  }

}



