import { Component, Input } from '@angular/core';
import { Events } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { UtilityProvider } from '../../../providers/utility/utility'
import { UserServiceProvider } from '../../../providers/user-service/user-service'
import { TranslateService } from '@ngx-translate/core';


@Component({
  selector: 'addressdetail',
  templateUrl: 'address-detail.html'
})
export class AddressDetailComponent {
  @Input() isEdit;
  @Input() address1;
  @Input() prefecture;
  @Input() ward;
  @Input() address2;
  @Input() address3;
  @Input() city;
  @Input() postalCode;
  private addressDetail: FormGroup;
  showPasswordChange = false;
  isValidZipCode = true;
  optionListing = [
    { 'name': 'Adachi' },
    { 'name': 'Arakawa' },
    { 'name': 'Bunkyo' },
    { 'name': 'Chiyoda' },
    { 'name': 'Ch' },
    { 'name': 'Edogawa' },
    { 'name': 'Itabashi' },
    { 'name': 'Katsushika' },
    { 'name': 'Kita' },
    { 'name': 'Kt' },
    { 'name': 'Meguro' },
    { 'name': 'Minato' },
    { 'name': 'Nakano' },
    { 'name': 'Nerima' },
    { 'name': 'ta' },
    { 'name': 'Setagaya' },
    { 'name': 'Shibuya' },
    { 'name': 'Shinagawa' },
    { 'name': 'Shinjuku' },
    { 'name': 'Suginami' },
    { 'name': 'Sumida' },
    { 'name': 'Tait' },
    { 'name': 'Toshima' },
  ];

  submitted = false;


  constructor(private formBuilder: FormBuilder, private utilityService: UtilityProvider, private userAPIService: UserServiceProvider, private event: Events, private translate: TranslateService) {
    setTimeout(() => {
      this.addressDetail = this.formBuilder.group({
        address1: [this.address1, Validators.required],
        address2: [this.address2, Validators.required],
        address3: [this.address3, Validators.required],
        city: [this.city, Validators.required],
        prefecture: [this.prefecture, Validators.required],
        ward: [this.ward],
        postalCode: [this.postalCode, Validators.required],
      });
    }, 50);

    this.event.unsubscribe('resetFromAddressDetail');
    this.event.subscribe('resetFromAddressDetail', res => {
      this.submitted = false;
      this.addressDetail = this.formBuilder.group({
        address1: [this.address1, Validators.required],
        address2: [this.address2, Validators.required],
        address3: [this.address3, Validators.required],
        city: [this.city, Validators.required],
        prefecture: [this.prefecture, Validators.required],
        ward: [this.ward],
        postalCode: [this.postalCode, Validators.required],
      });
    });
    // this.getAddress();
  }

  moveFocus(event, nextElement, index) {
    if (event.code == 'Enter') {
      if (index == 1) {
        this.getAddress();
      }
      nextElement.setFocus();
    }
  }

  showPassword() {
    this.showPasswordChange = true;
  }

  saveAddress() {
    this.submitted = true;

    if (!this.addressDetail.get('address1').valid) {
      // this.utilityService.presentToast('ERROR_LINE_1', true);
    } else if (!this.addressDetail.get('city').valid) {
      // this.utilityService.presentToast('ERROR_CITY', true);
    } else if (!this.addressDetail.get('postalCode').valid) {
      // this.utilityService.presentToast('ERROR_POSTAL_CODE', true);
    } else if (!this.addressDetail.get('prefecture').valid) {
      // this.utilityService.presentToast('ERROR_PREFECTURE', true);
    } else if (this.addressDetail.get('city').value.toLowerCase() == 'tokyo' && !this.addressDetail.get('ward').valid) {
      // this.utilityService.presentToast('ERROR_WARD', true);
    } else if (!this.isValidZipCode) {
      let errorMessage = "INVALID_ZIP";
      this.translate.get(errorMessage).subscribe(
        value => {
          this.utilityService.showAlert(value);
        })
    } else {
      this.submitted = false;
      let tmpData = {
        addressLine1: this.addressDetail.get('address1').value,
        city: this.addressDetail.get('city').value,
        postalCode: this.addressDetail.get('postalCode').value,
        prefecture: this.addressDetail.get('prefecture').value,
        wards: this.addressDetail.get('city').value.toLowerCase() == 'tokyo' ? this.addressDetail.get('ward').value : undefined,
      }
      if (this.addressDetail.get('address2').value && this.addressDetail.get('address2').value != '') {
        tmpData['addressLine2'] = this.addressDetail.get('address2').value
      }
      if (this.addressDetail.get('address3').value && this.addressDetail.get('address3').value != '') {
        tmpData['addressLine3'] = this.addressDetail.get('address3').value
      }
      let updateProfileData = { address: tmpData };
      this.userAPIService.updateProfile(updateProfileData).then(res => {
        this.event.publish('updateProfile');
      }).catch(error => {
        if (error['error']['error']) {
          this.utilityService.showAlert(error['error']['error']);
        }
      })
    }
  }

  isCityTokoyo() {
    return this.addressDetail.get('city').value != null && this.addressDetail.get('city').value.toLowerCase() == 'tokyo';
  }


  getAddress() {
    if (this.addressDetail.get('postalCode').valid) {
      let errorMessage = "INVALID_ZIP";
      this.translate.get(errorMessage).subscribe(
        value => {
          errorMessage = value;
        })
      this.userAPIService.getAddress(this.addressDetail.get('postalCode').value.replace("-", "")).then(res => {
        console.log('res', res);
        if (res['status'] == "OK") {
          let obj = res['results'][0].address_components;
          console.log(obj);
          if (obj.length < 5) {
            this.isValidZipCode = false;
            this.utilityService.showAlert(errorMessage);
            return;
          }
          this.addressDetail.controls['prefecture'].setValue(obj[3]['long_name']); // 
          this.addressDetail.controls['city'].setValue(obj[2]['long_name']);  // 
          this.addressDetail.controls['address1'].setValue(obj[1]['long_name']); // 
          this.isValidZipCode = true;
        } else {
          this.isValidZipCode = false;
          this.utilityService.showAlert(errorMessage);
        }
      }).catch(error => {
        this.isValidZipCode = false;
        this.utilityService.showAlert(errorMessage);
      })
    }
  }
}



