import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ToastController, AlertController, LoadingController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';

@Injectable()
export class UtilityProvider {
  imgeURI;
  constructor(public http: HttpClient, public toastCtrl: ToastController, private translate: TranslateService, private alertCtrl: AlertController, private loadingCtrl: LoadingController) {
  }

  presentToast(message, isNeedToTranslate) {
    if (isNeedToTranslate) {
      this.translate.get(message).subscribe(
        value => {
          let toast = this.toastCtrl.create({
            message: value,
            duration: 3000,
          });
          toast.present();
        })
    }
    else {
      const toast = this.toastCtrl.create({
        message: message,
        duration: 3000,
      });
      toast.present();
    }
  }

  showAlert(message) {
    let alertList = [{
      text: 'OK',
      handler: () => {

      }
    }]
    for (let alert of alertList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let title = 'iBeed Mari'
    this.translate.get('LOGO_NAME').subscribe(
      value => {
        title = value;
      })
    let alert = this.alertCtrl.create({
      title: title,
      message: message,
      buttons: alertList
    });
    alert.present();
  }
  showAlertWithAction(message, action) {
    let alertList = [{
      text: 'OK',
      handler: () => {
        action()
      }
    }]
    for (let alert of alertList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let title = 'iBeed Mari'
    this.translate.get('LOGO_NAME').subscribe(
      value => {
        title = value;
      })
    let alert = this.alertCtrl.create({
      title: title,
      message: message,
      buttons: alertList
    });
    alert.present();
  }

  hasLowerCase(str) {
    return (/[a-z]/.test(str));
  }

  hasUpperCase(str) {
    return (/[A-Z]/.test(str));
  }

  getLoader() {
    let loading = this.loadingCtrl.create({
      spinner: '',
      content: "0 %",
    });
    return loading;
  }
}



