import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, Events, ActionSheetController, Platform, LoadingController, Slides, AlertController } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service'
import { UserServiceProvider } from '../../providers/user-service/user-service'
import { UtilityProvider } from '../../providers/utility/utility'
import { TranslateService } from '@ngx-translate/core';
import { Keyboard } from '@ionic-native/keyboard';


@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {
  @ViewChild(Slides) slides: Slides;

  public query: string = 'personal';
  loggedUser
  isEdit = false;
  selectedProfileType = 'personal';
  familyMemberList = [];
  uploadPreview;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private auth: AuthServiceProvider,
    private event: Events,
    private userAPIService: UserServiceProvider,
    private camera: Camera,
    private platform: Platform,
    public actionsheetCtrl: ActionSheetController,
    public loadingCtrl: LoadingController,
    public actionSheetCtrl: ActionSheetController,
    public translate: TranslateService,
    public utility: UtilityProvider,
    public keyboard: Keyboard,
    public alertCtrl: AlertController, ) {
    platform.ready().then(() => {
      keyboard.onKeyboardShow().subscribe(() => {
        document.body.classList.add('keyboard-is-open');
        document.body.classList.remove('keyboard-is-close');
      });

      keyboard.onKeyboardHide().subscribe(() => {
        document.body.classList.remove('keyboard-is-open');
        document.body.classList.add('keyboard-is-close');
      });
    })
    this.getUserData();
    this.event.unsubscribe('updateFamilyList');
    this.event.subscribe('updateFamilyList', res => {
      this.getFamilyMember();
    });
    this.event.unsubscribe('updateProfile');
    this.event.subscribe('updateProfile', res => {
      this.isEdit = !this.isEdit
      this.getUserData();
    });
    this.event.unsubscribe('isEdit');
    this.event.subscribe('isEdit', res => {
      this.isEdit = true
    })
    this.event.unsubscribe('isEditFalse');
    this.event.subscribe('isEditFalse', res => {
      this.isEdit = false
      this.event.publish('resetFrom');
    })
  }

  ionViewWillEnter() {
    this.getProfileData();
    this.getFamilyMember();
    this.event.publish('trackView', 'On Settings');
  }

  ionViewWillLeave() {
    this.uploadPreview = undefined;
    if (this.isEdit) {
      this.isEdit = false;
      this.event.publish('resetFrom');
      this.event.publish('resetFromPersonalDetail');
      this.event.publish('resetFromAddressDetail');
    }
  }

  getUserData() {
    this.loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'));
    console.log('sds', this.loggedUser);

  }

  getFamilyMember() {
    this.userAPIService.getFamilyMember().then(res => {
      this.familyMemberList = res['list'];
    }).catch(error => {

    })
  }

  editForm() {
    this.isEdit = !this.isEdit;
    if (this.isEdit) {
      if (this.selectedProfileType == 'personal') {
        this.event.publish('trackView', 'On Personal edit');
      } else if (this.selectedProfileType == 'address') {
        this.event.publish('trackView', 'On Address edit');
      } else if (this.selectedProfileType == 'family') {
        this.event.publish('trackView', 'On Family edit');
      }
    } else {
      this.event.publish('trackView', 'On Settings');
    }
    if (!this.isEdit) {
      this.event.publish('resetFrom');
      this.event.publish('resetFromPersonalDetail');
      this.event.publish('resetFromAddressDetail');
    }
  }

  changeProfileType(profileType) {
    if (!this.isEdit) {
      if (profileType.value == 'personal') {
        console.log("personal")
        this.slides.slideTo(0, 0);
      }
      if (profileType.value == 'address') {
        console.log("address")
        this.slides.slideTo(1, 0);
      }
      if (profileType.value == 'family') {
        console.log("personal")
        this.slides.slideTo(2, 0);
      }

      this.selectedProfileType = profileType.value;

      if (this.isEdit) {
        this.editForm();
      }
    }
  }

  slideChanges($event) {
    if (this.slides._activeIndex == 3 && this.slides.swipeDirection == 'next') {
      this.slides.slideTo(2, 0.5);
    } else {
    }
  }
  slideChanged() {
    if (this.slides._activeIndex == 3 && this.slides.swipeDirection == 'next') {
      this.slides.slideTo(2, 0.5);
    } else {
      if (this.slides._activeIndex == 0) {
        this.query = 'personal';
      }
      if (this.slides._activeIndex == 1) {
        this.query = 'address';
      }
      if (this.slides._activeIndex == 2) {
        this.query = 'family';
      }
      this.selectedProfileType = this.query
    }
  }

  accountsOption() {
    let buttonList = [];
    if (this.selectedProfileType != 'family') {
      buttonList.push({
        text: 'EDIT',
        handler: () => {
          this.editForm();
        },
      });
    }
    buttonList.push({
      text: 'SETTING_LOGOUT',
      handler: () => {
        this.logoutBtn();
      },
    });
    buttonList.push({
      text: 'SETTING_DA',
      cssClass: 'delete-account',
      handler: () => {
        this.userDelete();
      },
    });
    buttonList.push({
      text: 'CANCEL',
      role: 'cancel',
      icon: !this.platform.is('ios') ? 'close' : null,
      handler: () => {
      },
    });
    for (let alert of buttonList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let title = 'SETTING_SYA';
    this.translate.get(title).subscribe(
      value => {
        title = value;
      })
    const actionSheet = this.actionsheetCtrl.create({
      title: title,
      cssClass: 'select-action-sheet',
      buttons: buttonList,
    });
    actionSheet.present();
  }

  alertUserDelete(msg) {
    let alertList = [{
      text: 'OK',
      handler: () => {
        this.auth.clearStorageFor('LoggedUser')
        this.auth.clearStorageFor('isUserLogged')
        this.event.publish('logout');
      }
    }]
    for (let alert of alertList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let title = 'iBeed Mari'
    this.translate.get('LOGO_NAME').subscribe(
      value => {
        title = value;
      })

    let alert = this.alertCtrl.create({
      title: title,
      message: msg,
      buttons: alertList
    });
    alert.present();
  }


  userDelete() {

    let alertList = [{
      text: 'YES',
      handler: () => {
        this.userAPIService.deleteUser().then(res => {
          this.alertUserDelete(res['msg']);
        }).catch(error => {

        })
      }
    }, {
      text: 'NO',
      handler: () => {
      }
    }]
    for (let alert of alertList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let title = 'iBeed Mari'
    this.translate.get('LOGO_NAME').subscribe(
      value => {
        title = value;
      })
    let message = 'Are you sure, you want to delete account ?'
    this.translate.get('DELETE_CONFIRMATION').subscribe(
      value => {
        message = value;
      })
    let alert = this.alertCtrl.create({
      title: title,
      message: message,
      buttons: alertList
    });
    alert.present();
  }

  logoutBtn() {
    let alertList = [{
      text: 'YES',
      handler: () => {
        this.userAPIService.logOut().then(res => {

        }).catch(error => {

        })
        this.auth.clearStorageFor('LoggedUser')
        this.auth.clearStorageFor('isUserLogged')
        this.event.publish('logout');
      }
    }, {
      text: 'NO',
      handler: () => {
      }
    }]
    for (let alert of alertList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let title = 'iBeed Mari'
    this.translate.get('LOGO_NAME').subscribe(
      value => {
        title = value;
      })
    let message = 'Are you sure, you want to logout ?'
    this.translate.get('LOGOUT_CONFIRMATION').subscribe(
      value => {
        message = value;
      })
    let alert = this.alertCtrl.create({
      title: title,
      message: message,
      buttons: alertList
    });
    alert.present();
  }

  takePicture() {
    let buttonList = [{
      text: 'PHOTO_LIBRARY',
      handler: () => {
        const type = this.camera.PictureSourceType.PHOTOLIBRARY;
        this.selectPicture(type);
      },
    }, {
      text: 'PHOTO_CAMERA',
      handler: () => {
        const type = this.camera.PictureSourceType.CAMERA;
        this.selectPicture(type);
      },
    }, {
      text: 'CANCEL',
      role: 'cancel', // will always sort to be on the bottom
      icon: !this.platform.is('ios') ? 'close' : null,
      handler: () => {
        //  UtilityServiceProvider.log('Cancel clicked');
      },
    },];
    for (let alert of buttonList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let localizeTitle = 'PHOTO_SOURCE'
    this.translate.get(localizeTitle).subscribe(
      value => {
        localizeTitle = value;
      })

    const actionSheet = this.actionsheetCtrl.create({
      title: localizeTitle,
      cssClass: 'action-sheets-basic-page',
      buttons: buttonList,
    });
    actionSheet.present();
  }


  changeImage() {
    this.takePicture();
  }


  selectPicture(sourceType) {
    const options: CameraOptions = {
      mediaType: this.camera.MediaType.PICTURE, // PHOTOLIBRARY
      quality: 100,
      targetHeight: 200,
      targetWidth: 200,
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType,
      encodingType: this.camera.EncodingType.PNG,
      saveToPhotoAlbum: false,
    };
    let loader = this.loadingCtrl.create();
    loader.present();
    this.camera.getPicture(options).then((imagePath) => {
      this.uploadPreview = 'data:image/jpeg;base64,' + imagePath;
      loader.dismissAll();
    }, (err) => {
      loader.dismissAll();
    });

  }

  getProfileData() {
    this.userAPIService.getProfile().then(res => {
      this.getUserData();
    }).catch(error => {

    })
  }

}



