import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { Slides } from 'ionic-angular';


import { NotificationServiceProvider } from '../../providers/notification-service/notification-service'
import { AuthServiceProvider } from '../../providers/auth-service/auth-service'
import { UtilityProvider } from '../../providers/utility/utility'

import { InsuranceDetailPage } from '../insurance-detail/insurance-detail'

@IonicPage()
@Component({
  selector: 'page-notifications',
  templateUrl: 'notifications.html',
})
export class NotificationsPage {
  selectNotificationType = 'PERSONAL';
  @ViewChild(Slides) slides: Slides;
  personalNotifications = [];
  generalNotifications = [];
  personalNotificationsKeys = [];
  generalNotificationsKeys = [];
  groupedPersonalNotifications = {};
  groupedGeneralNotifications = {};
  selectedNotification;
  unReadCount = 0;
  constructor(public navCtrl: NavController, public navParams: NavParams, private notificationAPI: NotificationServiceProvider, private utilityService: UtilityProvider, private event: Events, protected auth: AuthServiceProvider) {
  }

  ionViewWillEnter() {
    this.getNotifications(true);
    this.event.publish('trackView', 'On Notifcations');
    this.event.unsubscribe('fetchNotificationData');
    this.event.subscribe('fetchNotificationData', () => {
      this.getNotifications(false);
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NotificationsPage');
  }

  getNotifications(updateNotificationCount) {
    this.notificationAPI.getNotifications().then(res => {
      if (updateNotificationCount) {
        this.event.publish('updateNotifications', '');
      }
      this.personalNotificationsKeys = [];
      this.generalNotificationsKeys = [];
      this.groupedPersonalNotifications = {};
      this.groupedGeneralNotifications = {};
      this.unReadCount = 0;
      this.personalNotifications = res['personalNotifications']
      for (const notification of this.personalNotifications) {
        if (notification.status === 'Unread') {
          this.unReadCount += 1;
        }
      }
      this.filterNotification(this.personalNotifications, this.groupedPersonalNotifications, this.personalNotificationsKeys)
      this.generalNotifications = res['generalNotifications']
      this.filterNotification(this.generalNotifications, this.groupedGeneralNotifications, this.generalNotificationsKeys)
    }).catch(error => {

    })
  }

  filterNotification(sourceList, destList, keyDestList) {
    let groupDate;
    for (const notification of sourceList) {
      const notificationDate = notification.createdAt.split('T')[0]
      if (!groupDate || groupDate != notificationDate) {
        groupDate = notificationDate;
      }
      if (groupDate in destList) {
        let notificationList = destList[groupDate];
        notificationList.push(notification)
      } else {
        keyDestList.push(groupDate);
        destList[groupDate] = [notification];
      }
    }
  }

  notificationSelected(insurance) {
    if (insurance.value == 'ALL') {
      this.slides.slideTo(1, 0);
    }
    if (insurance.value == 'PERSONAL') {
      this.slides.slideTo(0, 0);
    }
    this.selectNotificationType = insurance.value;
  }

  slideChanges($event) {
    if (this.slides._activeIndex == 2 && this.slides.swipeDirection == 'next') {
      this.slides.slideTo(1, 0.5);
    } else {
    }
  }

  slideChanged(event) {
    if (this.slides._activeIndex == 2 && this.slides.swipeDirection == 'next') {
      this.slides.slideTo(1, 0.5);
    } else {
      if (this.slides._activeIndex == 0) {
        this.selectNotificationType = 'PERSONAL';
      }
      if (this.slides._activeIndex == 1) {
        this.selectNotificationType = 'ALL';
      }
    }
  }

  updateNotificationStatus(notification, isShowAlert) {
    if (notification.status === 'Unread') {
      const notificationData = {
        id: notification._id,
        status: 'Read'
      }
      this.notificationAPI.updateNotificationStatus(notificationData).then(res => {
        notification.status = "Read";
        if (isShowAlert) {
          this.utilityService.showAlert(res['msg']);
        }
        this.getNotifications(false);
        this.event.publish('updateNotifications', '');
      }).catch(error => {
        let tmpDic = error.error;
        if (tmpDic.msg) {
          this.utilityService.showAlert(tmpDic.msg);
        }
      })
    }
  }

  showDetail(notification) {
    if (this.selectNotificationType == 'PERSONAL') {
      console.log('notification', notification);
      let insuranceId = notification.insuranceId;
      this.navCtrl.push(InsuranceDetailPage, { insuranceId: insuranceId });
      this.updateNotificationStatus(notification, false);
    } else {
      this.selectedNotification = notification;
    }
  }

  closeDetail() {
    this.selectedNotification = undefined;
  }
}



