import { Component } from '@angular/core';
import { DatePipe } from '@angular/common'
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NavController, LoadingController, NavParams, Platform, ActionSheetController, Events } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { DatePicker } from '@ionic-native/date-picker';
import { TranslateService } from '@ngx-translate/core';
import { Keyboard } from '@ionic-native/keyboard';
import { FileTransfer } from '@ionic-native/file-transfer';

import createNumberMask from 'text-mask-addons/dist/createNumberMask'

import { UtilityProvider } from '../../providers/utility/utility'
import { InsuranceServiceProvider } from '../../providers/insurance-service/insurance-service'
import { UserServiceProvider } from '../../providers/user-service/user-service'
import { AuthServiceProvider } from '../../providers/auth-service/auth-service'
import { BeneficiaryPage } from '../beneficiary/beneficiary';

import { SharePolicyPage } from '../share-policy/share-policy'

@Component({
  selector: 'page-insurance-management',
  templateUrl: 'insurance-management.html',
})
export class InsuranceManagementPage {
  calendar = {
    mode: 'month',
    currentDate: new Date()
  };
  companyTerms
  selectedCategory = 1;
  categoryList = [{ id: 1, image: "assets/imgs/mini-icon-1.svg", title: "AUTO", name: "Auto" },
  { id: 2, image: "assets/imgs/mini-icon-2.svg", title: "LIFE", name: "Life" },
  { id: 3, image: "assets/imgs/mini-icon-3.svg", title: "HEALTH", name: "Health" },
  { id: 4, image: "assets/imgs/mini-icon-4.svg", title: "PET", name: "Pet" },
  { id: 5, image: "assets/imgs/mini-icon-5.svg", title: "HOME", name: "Home" }];
  policyFrequencyList = ['POLICY_FRE_ANNUALY', 'POLICY_FRE_BI_ANNUALY', 'POLICY_FRE_QUARTERLY', 'POLICY_FRE_MONTHLY', 'POLICY_FRE_ONE_TIME'];
  companyList = [];
  productList = [];
  submitted = false;
  imageURI: any;
  imageFileName: any;
  loadimgProfile: boolean = true;
  private insurance: FormGroup;
  maxDate;
  minDate;
  uploadPrevciew = '';
  showingPriviews = false;
  familyMemberList;
  beneficiaryData = [];
  loader;
  loggedUser;
  isEditing = false;
  editInsuranceDetail;
  isFromCamera;
  isValidDate = true;
  handleScrollTiming: any;
  public premiumAmtmask
  public hotLineMask
  public maxClainAmtmask
  selectedUsers;
  constructor(public navCtrl: NavController, public navParams: NavParams,
    private camera: Camera,
    private platform: Platform,
    public actionsheetCtrl: ActionSheetController,
    public loadingCtrl: LoadingController,
    private formBuilder: FormBuilder,
    private utilityService: UtilityProvider,
    private auth: AuthServiceProvider,
    public datepipe: DatePipe,
    private userAPISerivce: UserServiceProvider,
    private translate: TranslateService,
    private datePicker: DatePicker,
    private event: Events,
    private transfer: FileTransfer,
    private keyboard: Keyboard,
    private insuranceAPIService: InsuranceServiceProvider) {
    platform.ready().then(() => {
      keyboard.onKeyboardShow().subscribe(() => {
        document.body.classList.add('keyboard-is-open');
        document.body.classList.remove('keyboard-is-close');
      });

      keyboard.onKeyboardHide().subscribe(() => {
        document.body.classList.remove('keyboard-is-open');
        document.body.classList.add('keyboard-is-close');
      });
    })
    const numberMask = createNumberMask({
      prefix: '',
      thousandsSeparatorSymbol: ',',
      allowLeadingZeroes: false
    })
    this.premiumAmtmask = numberMask
    const hotLineMaskNumber = createNumberMask({
      prefix: '0',
      thousandsSeparatorSymbol: '',
      allowLeadingZeroes: true
    })
    this.hotLineMask = hotLineMaskNumber
    this.maxClainAmtmask = numberMask
    this.insurance = this.formBuilder.group({
      company: ['', Validators.required],
      product: ['', Validators.required],
      policyFrequency: ['', Validators.required],
      insured: ['', Validators.required],
      policyHolder: ['', Validators.required],
      policyNumber: ['', Validators.required],
      startDate: ['', Validators.required],
      endDate: ['', Validators.required],
      premiumAmount: ['', Validators.required],
      maxClaimAmount: ['', Validators.required],
      proof: ['', Validators.required],
      imageURI: ['', Validators.required],
      hotLineNo: ['', Validators.required],
      share: ['']
    });
    for (let category of this.categoryList) {
      this.translate.get(category.title).subscribe(
        value => {
          category.title = value;
        })
    }

    this.loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'));

    this.userAPISerivce.getBeneficiaryFamilyMember().then(res => {
      this.familyMemberList = res['list'];
      this.beneficiaryData = [];
      this.familyMemberList.map(familyMember => {
        this.beneficiaryData.push({ familyMemberId: familyMember._id, percentageAmount: (1 / this.familyMemberList.length) * 100.0, isBeneficiary: true, relationShip: familyMember.relationShip })
      })
    }).catch(error => {
      this.familyMemberList = [];
    })
    let minyear = new Date();
    let pastYear = new Date(minyear.setFullYear(minyear.getFullYear() - 100));
    this.minDate = pastYear.getFullYear();
    minyear = new Date();
    let maxyear = new Date(minyear.setFullYear(minyear.getFullYear() + 100));
    this.maxDate = maxyear.getFullYear();
    if (navParams.get('editInsurance')) {
      setTimeout(() => {
        this.editInsuranceDetail = JSON.parse(navParams.get('editInsurance'));
        if (this.editInsuranceDetail) {
          for (let category of this.categoryList) {
            if (this.editInsuranceDetail.insuranceProduct.policyType == category.name) {
              this.selectedCategory = category.id
              break;
            }
          }
          let dates = new Date(this.editInsuranceDetail.startDate);
          let day = dates.getDate();
          let monthIndex = dates.getMonth();
          let year = dates.getFullYear();
          let tmpStartDate = year + (monthIndex >= 9 ? '/' : '/0') + (monthIndex + 1) + (day > 9 ? '/' : '/0') + day;
          dates = new Date(this.editInsuranceDetail.endDate);
          day = dates.getDate();
          monthIndex = dates.getMonth();
          year = dates.getFullYear();
          let tmpEndDate = year + (monthIndex >= 9 ? '/' : '/0') + (monthIndex + 1) + (day > 9 ? '/' : '/0') + day;
          this.isEditing = true;
          this.insurance = this.formBuilder.group({
            company: [this.editInsuranceDetail.organization._id, Validators.required],
            product: [this.editInsuranceDetail.insuranceProduct._id, Validators.required],
            policyFrequency: [this.editInsuranceDetail.premiumPaymentFreq, Validators.required],
            policyNumber: [this.editInsuranceDetail.policyNumber, Validators.required],
            startDate: [tmpStartDate, Validators.required],
            endDate: [tmpEndDate, Validators.required],
            premiumAmount: [this.editInsuranceDetail.premiumAmount, Validators.required],
            maxClaimAmount: [this.editInsuranceDetail.maxClaimAmount, Validators.required],
            proof: [this.editInsuranceDetail.signedUrl, Validators.required],
            imageURI: [this.editInsuranceDetail.signedUrl, Validators.required],
            insured: [this.editInsuranceDetail.insured, Validators.required],
            policyHolder: [this.editInsuranceDetail.policyHolder, Validators.required],
            hotLineNo: [this.editInsuranceDetail.hotlinePhoneNo ? this.editInsuranceDetail.hotlinePhoneNo : '', Validators.required],
            share: ['']
          });
          this.showingPriviews = true;
          this.uploadPrevciew = this.editInsuranceDetail.signedUrl;
          for (let tmpCategory of this.categoryList) {
            if (tmpCategory.name == this.editInsuranceDetail.insuranceProduct.policyType) {
              this.selectedCategory = tmpCategory.id;
              break;
            }
          }
          this.getOrganizationList();
          this.getProductList();
        }
      }, 1000);
    }
    this.getOrganizationList();

  }

  ionViewWillEnter() {
    if (window.localStorage.getItem('selectedUsers')) {
      this.selectedUsers = JSON.parse(window.localStorage.getItem('selectedUsers'));
      console.log('selectedUsers', this.selectedUsers);
      // window.localStorage.removeItem('selectedUsers');
      let tmpValue = [];
      for (let user of this.selectedUsers) {
        if (user.firstName + user.lastName) {
          tmpValue.push(user.firstName + user.lastName);
        } else {
          tmpValue.push(user.email);
        }
      }
      this.insurance.controls['share'].setValue(tmpValue);
    }
    if (this.editInsuranceDetail) {
      this.event.publish('trackView', 'On Insurance Edit');
    } else {
      this.event.publish('trackView', 'On Insurance Create');
    }
  }

  getOrganizationList() {
    const currentObj = this;
    let categoryName;
    for (let category of this.categoryList) {
      if (category.id == this.selectedCategory) {
        categoryName = category.name;
        break;
      }
    }
    let loader = this.loadingCtrl.create();
    loader.present();
    currentObj.insuranceAPIService.getOrganisationList(categoryName).then(res => {
      loader.dismiss();
      currentObj.companyList = res['data'];
    }).catch(error => {
      loader.dismiss();
    })
  }

  categorySelect(category) {
    this.selectedCategory = category.id;
    this.getOrganizationList();
    this.insurance.controls['company'].setValue('')
    this.insurance.controls['product'].setValue('')
  }

  takePicture() {
    let buttonList = [{
      text: 'PHOTO_LIBRARY',
      handler: () => {
        const type = this.camera.PictureSourceType.PHOTOLIBRARY;
        this.isFromCamera = false;
        this.selectPicture(type);
      },
    }, {
      text: 'PHOTO_CAMERA',
      handler: () => {
        const type = this.camera.PictureSourceType.CAMERA;
        this.isFromCamera = true;
        this.selectPicture(type);
      },
    }, {
      text: 'CANCEL',
      role: 'cancel', // will always sort to be on the bottom
      icon: !this.platform.is('ios') ? 'close' : null,
      handler: () => {
      },
    },];
    for (let alert of buttonList) {
      this.translate.get(alert['text']).subscribe(
        value => {
          alert['text'] = value;
        })
    }
    let localizeTitle = 'PHOTO_SOURCE'
    this.translate.get(localizeTitle).subscribe(
      value => {
        localizeTitle = value;
      })

    const actionSheet = this.actionsheetCtrl.create({
      title: localizeTitle,
      cssClass: 'action-sheets-basic-page',
      buttons: buttonList,
    });
    actionSheet.present();
  }

  changeImage() {
    this.takePicture();
  }

  removeImage() {
    this.showingPriviews = false;
  }

  selectPicture(sourceType) {
    this.loadimgProfile = false;
    const options: CameraOptions = {
      mediaType: this.camera.MediaType.PICTURE, // PHOTOLIBRARY
      quality: 100,
      destinationType: this.camera.DestinationType.FILE_URI,
      sourceType: sourceType,
      encodingType: this.camera.EncodingType.PNG,
      saveToPhotoAlbum: false,
      correctOrientation: true,
    };
    let loader = this.loadingCtrl.create();

    loader.present();
    this.camera.getPicture(options).then((imagePath) => {
      var uploadImage = imagePath;
      if (this.platform.is('ios')) {
        uploadImage = imagePath.replace(/^file:\/\//, '');
      }
      this.showingPriviews = true;
      this.uploadPrevciew = uploadImage;
      this.insurance.controls['proof'].setValue(uploadImage);
      this.insurance.controls['imageURI'].setValue(uploadImage);
      loader.dismiss();
    }, (err) => {
      loader.dismiss();
    });
  }

  companySelected(event) {
    this.insurance.controls['company'].setValue(event);
    this.getProductList();
  }

  getProductList() {
    if (this.insurance.get('company').value != '') {
      let categoryName;
      for (let category of this.categoryList) {
        if (category.id == this.selectedCategory) {
          categoryName = category.name;
          break;
        }
      }
      if (categoryName) {
        if (!this.isEditing) {
          this.insurance.controls['product'].setValue('');
        }
        let loader = this.loadingCtrl.create();
        loader.present();
        this.insuranceAPIService.getProductList(this.insurance.get('company').value, categoryName).then(res => {
          loader.dismiss();
          this.productList = res['data'];
          if (this.productList.length == 0) {
            this.utilityService.presentToast('ERROR_NO_PRODUCT', true);
          }
        }).catch(error => {
          loader.dismiss();
          if (error['error']['error']) {
            this.utilityService.showAlert(error['error']['error']);
          }
          console.error(error);
        })
      }
    }
  }

  onPolicyNumberInputTime(event) {
    const pattern = new RegExp("^[a-zA-Z0-9-]+$");
    if (!pattern.test(event.data)) {
      let tmpStr = this.insurance.get('policyNumber').value;
      this.insurance.controls['policyNumber'].setValue(tmpStr.replace(event.data, ''));
    }
  }

  ionViewWillLeave() {
    document.body.classList.remove('keyboard-is-close');
  }

  baneficiaryScreen() {
    this.validateDate()
    this.submitted = true;
    if (this.insurance.get('company').valid && this.insurance.get('product').valid && this.insurance.get('policyFrequency').valid && this.insurance.get('policyNumber').valid && this.insurance.get('startDate').valid && this.insurance.get('endDate').valid && this.insurance.get('premiumAmount').valid && this.insurance.get('maxClaimAmount').valid && this.insurance.get('policyHolder').valid && this.insurance.get('proof').valid && !this.isValidDate) {
      this.submitted = false;
      var insuranceRequestData = {};
      let tmpPreAmt = this.insurance.get('premiumAmount').value
      let tmpMaxClaimAmt = this.insurance.get('maxClaimAmount').value
      if (!this.isEditing) {
        tmpPreAmt.replace(",", "").replace(",", "").replace(",", "");
        tmpMaxClaimAmt.replace(",", "").replace(",", "").replace(",", "");
      }
      insuranceRequestData["organizationId"] = this.insurance.get('company').value;
      insuranceRequestData["insuranceProductId"] = this.insurance.get('product').value;
      insuranceRequestData["policyNumber"] = this.insurance.get('policyNumber').value;
      insuranceRequestData["premiumPaymentFreq"] = this.insurance.get('policyFrequency').value;
      insuranceRequestData["premiumAmount"] = tmpPreAmt;
      insuranceRequestData["maxClaimAmount"] = tmpMaxClaimAmt;
      insuranceRequestData["startDate"] = this.insurance.get('startDate').value;
      insuranceRequestData["endDate"] = this.insurance.get('endDate').value;
      insuranceRequestData["policyDoc"] = this.insurance.get('proof').value;
      insuranceRequestData["imageURI"] = this.insurance.get('imageURI').value;
      insuranceRequestData["insured"] = this.insurance.get('insured').value;
      insuranceRequestData["policyHolder"] = this.insurance.get('policyHolder').value;
      if (this.insurance.get('hotLineNo').valid) {
        insuranceRequestData["hotlinePhoneNo"] = this.insurance.get('hotLineNo').value;
      }
      this.utilityService.imgeURI = insuranceRequestData["imageURI"]
      if (this.isEditing) {
        insuranceRequestData["id"] = this.editInsuranceDetail['_id'];
        insuranceRequestData["document"] = this.editInsuranceDetail['documents'];
        insuranceRequestData["administrator"] = this.editInsuranceDetail['administrator'];
        insuranceRequestData["isSelfBeneficiary"] = this.editInsuranceDetail['isSelfBeneficiary'];
        if (this.editInsuranceDetail.benificiariesMember.length > 0) {
          this.beneficiaryData = []
          for (let familyMember of this.familyMemberList) {
            let isAlreadyBeneficiary = false;
            let tmpBeneficiary;
            for (let beneficiary of this.editInsuranceDetail.benificiariesMember) {
              tmpBeneficiary = beneficiary;
              if (familyMember._id == beneficiary._id) {
                isAlreadyBeneficiary = true;
                break;
              }
            }
            if (isAlreadyBeneficiary) {
              this.beneficiaryData.push({ familyMemberId: tmpBeneficiary._id, percentageAmount: tmpBeneficiary.percentageAmount, isBeneficiary: true, relationShip: tmpBeneficiary.relationship })
            }
            else {
              this.beneficiaryData.push({ familyMemberId: familyMember._id, percentageAmount: 0, isBeneficiary: false, relationShip: familyMember.relationship })
            }
          }
        }
      }
      this.auth.localStore('familyMemberList', JSON.stringify(this.familyMemberList))
      this.auth.localStore('beneficiaryList', JSON.stringify(this.beneficiaryData))
      this.navCtrl.push(BeneficiaryPage, { insuranceRequest: JSON.stringify(insuranceRequestData), isEditing: this.isEditing });
    }
  }

  dataURItoBlob = (dataURI) => {
    const byteString = atob(dataURI.split(',')[1]);
    const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];

    const ab = new ArrayBuffer(byteString.length);
    const ia = new Uint8Array(ab);
    for (let i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }

    const bb = new Blob([ab], {
      type: mimeString,
    });
    return bb;
  }

  showDate(isStartDate) {
    let done;
    this.translate.get('DATE_DONE').subscribe(
      value => {
        done = value;
      })
    let cancel;
    this.translate.get('CANCEL').subscribe(
      value => {
        cancel = value;
      })
    let date = isStartDate ? this.insurance.get('startDate').value == '' ? new Date() : new Date(this.insurance.get('startDate').value) : this.insurance.get('endDate').value == '' ? new Date() : new Date(this.insurance.get('endDate').value);
    this.datePicker.show({
      date: date,
      mode: 'date',
      androidTheme: this.datePicker.ANDROID_THEMES.THEME_HOLO_DARK,
      okText: done,
      cancelText: cancel,
      locale: 'ja_JP',
      doneButtonLabel: done,
      cancelButtonLabel: cancel
    }).then(
      date => {
        console.log(date);
        if (date) {
          let dates = new Date(date);
          let day = dates.getDate();
          let monthIndex = dates.getMonth();
          let year = dates.getFullYear();
          let tmpStartDate = year + (monthIndex >= 9 ? '/' : '/0') + (monthIndex + 1) + (day > 9 ? '/' : '/0') + day;
          if (isStartDate) {
            this.insurance.controls['startDate'].setValue(tmpStartDate);
          } else {
            this.insurance.controls['endDate'].setValue(tmpStartDate);
          }
          this.validateDate();
        }
      },
      err => {
        console.log('Error occurred while getting date: ', err)
      }
    );
  }

  validateDate() {
    let startDate: Date = new Date(this.insurance.get('startDate').value);
    let endDate: Date = new Date(this.insurance.get('endDate').value);
    this.isValidDate = (startDate >= endDate)
  }

  moveFocus(event, nextElement) {
    if (event.code == 'Enter' && nextElement) {
      nextElement.setFocus();
    }
  }

  showUserList() {
    window.localStorage.setItem('selectedUsers', JSON.stringify(this.selectedUsers));
    this.navCtrl.push(SharePolicyPage);
  }

  onChange(event) {
    console.log(event);
  }


  isCol3(index) {
    if (index == 0 || index == this.categoryList.length - 1) {
      return ''
    }
    else {
      return null
    }
  }

  isCol2(index) {
    if (index != 0 && index != this.categoryList.length - 1) {
      return ''
    }
    else {
      return null
    }
  }
}



