import { Component } from '@angular/core';
import { Platform, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TabsPage } from '../pages/tabs/tabs';
import { SigninPage } from '../pages/signin/signin';
import { WalkthroughPage } from '../pages/walkthrough/walkthrough';
import { Push, PushObject, PushOptions } from '@ionic-native/push';


import { TranslateService } from '@ngx-translate/core';
import { AuthServiceProvider } from '../providers/auth-service/auth-service'
import { FirebaseAnalyticsProvider } from '../providers/firebase-analytics/firebase-analytics'
import { UserServiceProvider } from '../providers/user-service/user-service'
import { UserEmailServiceProvider } from '../providers/user-email-service/user-email-service'
import { UtilityProvider } from '../providers/utility/utility'
import { AppVersion } from '@ionic-native/app-version';

declare var FirebaseCrashlytics
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage: any = SigninPage;
  alreadyLogged = true
  version = '1.0';
  constructor(private platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, private auth: AuthServiceProvider, private event: Events, private translate: TranslateService, private userAPI: UserServiceProvider, private fireBaseAnalytics: FirebaseAnalyticsProvider, public push: Push, private util: UtilityProvider, private userEmailService: UserEmailServiceProvider, private appVersion: AppVersion) {
    platform.ready().then(() => {
      this.appVersion.getVersionNumber().then(res => {
        console.log('res', res);
        this.version = res;
      });
      this.initPushNotification();
      // var crashlytics = FirebaseCrashlytics.initialise();
      // crashlytics.crash();
      let isWalkThorghComplete = auth.getLocalStore('isWalkThorghComplete');
      if (isWalkThorghComplete) {
        this.rootPage = WalkthroughPage;
      } else {
        let isLoggedUser = auth.getLocalStore('isUserLogged');
        if (isLoggedUser == 'true') {
          this.rootPage = TabsPage;
          let loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'));
          console.log('loggedUser', loggedUser);
        }
      }
      this.checkMigrationFinished();
      this.updateLastActiveTime();
      window.addEventListener('keyboardWillHide', () => {
        document.body.classList.remove('keyboard-is-open');
        document.body.classList.add('keyboard-is-close');
      });
      window.addEventListener('keyboardWillShow', (ev) => {
        document.body.classList.add('keyboard-is-open');
        document.body.classList.remove('keyboard-is-close');
      });

      setTimeout(() => {
        splashScreen.hide();
      }, 1000);

      this.event.subscribe('trackView', res => {
        this.fireBaseAnalytics.logPageViewEvent(res);
      })
    });
    const currentObj = this;
    currentObj.event.unsubscribe('logout');
    currentObj.event.subscribe('logout', res => {
      this.fireBaseAnalytics.logPageViewEvent('User logged out');
      currentObj.rootPage = SigninPage;
    })
    currentObj.event.unsubscribe('checkDuplicateMail');
    currentObj.event.subscribe('checkDuplicateMail', res => {
      let loggedUser = JSON.parse(this.auth.getLocalStore('LoggedUser'));
      currentObj.userEmailService.checkMigrationLink(loggedUser)
    })
    currentObj.event.unsubscribe('loggedIn');
    currentObj.event.subscribe('loggedIn', res => {
      this.fireBaseAnalytics.logPageViewEvent('User logged In');
      currentObj.rootPage = TabsPage;
    })
    currentObj.event.unsubscribe('walkThrough');
    currentObj.event.subscribe('walkThrough', res => {
      currentObj.rootPage = WalkthroughPage;
    })
    currentObj.event.unsubscribe('emailExists');
    currentObj.event.subscribe('emailExists', res => {
      this.alreadyLogged = false
      this.userEmailService.socialUser = res['userData']
      console.log('emailExists', res);

      if ('email' in res && res['email'] && res['email'] != '') {
        currentObj.userEmailService.existingEmailAlert(res['email'], true);
      } else {
        currentObj.userEmailService.emailForSocial(res['email']);
      }
    })
    let tmpLangugageList = navigator.language.split("-");
    let languageCode = tmpLangugageList[0].toLowerCase()
    if (languageCode == 'en' || languageCode == 'ja') {
      this.auth.langCode = languageCode;
      translate.setDefaultLang(languageCode);
      translate.use(languageCode);
    }
    else {
      translate.setDefaultLang('en');
      translate.use('en');
    }
    platform.resume.subscribe(() => {
      this.updateLastActiveTime();
      this.event.publish('updateNotifications');
      this.checkMigrationFinished();
    });
  }


  updateLastActiveTime() {
    this.userAPI.updateLastActiveTime().then(res => {

    }).catch(error => {

    })
  }


  initPushNotification() {
    if (!this.platform.is('cordova')) {
      console.warn('Push notifications not initialized. Cordova is not available - Run in physical device');
      return;
    }
    const options: PushOptions = {
      android: {
        clearBadge: false,
        senderID: '390981059844'
      }, ios: {
        alert: true,
        badge: true,
        sound: true,
        clearBadge: false,
      },
      windows: {}
    };
    const pushObject: PushObject = this.push.init(options);

    pushObject.on('registration').subscribe((data: any) => {
      console.log('device token -> ' + data.registrationId);
      window.localStorage.setItem('DeviceToken', data.registrationId);
      //TODO - send device token to server
      let isLoggedUser = this.auth.getLocalStore('isUserLogged');
      if (isLoggedUser == 'true') {
        let deviceData = {
          platform: this.platform.is('ios') ? 'iOS' : 'Android',
          deviceToken: window.localStorage.getItem('DeviceToken'),
          version: this.version
        }
        console.log('deviceData', deviceData);
        this.userAPI.updateDeviceToken(deviceData).then(res => {

        }).catch(error => {
          console.log('Erroe in registration', error);
        })
      }
    });

    pushObject.on('notification').subscribe((data: any) => {
      console.log('message -> ' + data.message);
      //if user using app and push notification comes
      if (data.additionalData.foreground) {
        // if application open, show popup

      } else {
        //if user NOT using app and push notification comes
        //TODO: Your logic on click of push notification directly
        console.log('Push notification clicked');
      }
    });

    pushObject.on('error').subscribe(error => console.error('Error with Push plugin' + error));
  }

  checkMigrationFinished() {
    this.userAPI.migrationFinished().then(res => {
      if ('flag' in res && res['flag'] == 1) {
        this.userEmailService.closeEmailAlert();
        this.translate.get('ACOOUNT_MERGED').subscribe(value => {
          this.util.showAlert(value);
        })
        this.userAPI.getProfile().then(res => {

        }).catch(error => {

        })
      }
    }).catch(error => {

    })
  }


}


