import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, Events } from 'ionic-angular';
import { TabsPage } from '../../pages/tabs/tabs';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';
import { Slides } from 'ionic-angular';



@Component({
  selector: 'page-walkthrough',
  templateUrl: 'walkthrough.html',
})
export class WalkthroughPage {
  @ViewChild(Slides) slides: Slides;

  walkThroughScreens = ['assets/imgs/1.png', 'assets/imgs/2.png', 'assets/imgs/3.png', 'assets/imgs/4.png', 'assets/imgs/5.png', 'assets/imgs/6.png', 'assets/imgs/7.png']
  walkThroughScreensTitle = [
    '',
   '', 
   '', 
   '', 
   '', 
   '',
  '']
  walkThroughScreensText = [
    '', 
  '', 
  '', 
  '', 
  '', 
  '',
  '']
  constructor(public navCtrl: NavController, public navParams: NavParams) {
    window.localStorage.setItem('isWalkThorghComplete', 'false')
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad WalkthroughPage');
  }

  goToHome() {
    window.localStorage.removeItem('isWalkThorghComplete');
    this.navCtrl.setRoot(TabsPage, { moveToSetting: true });
  }

  slideChanged(event) {
    if (this.slides._activeIndex == this.walkThroughScreens.length && this.slides.swipeDirection == 'next') {
      this.slides.slideTo(this.walkThroughScreens.length - 1, 0.5);
    }
  }
}



