import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiServiceProvider } from '../api-service/api-service';

@Injectable()
export class NotificationServiceProvider {

  constructor(public http: HttpClient, private apiService: ApiServiceProvider) {
    console.log('Hello NotificationServiceProvider Provider');
  }

  registerDevice(notificationData) {
    return new Promise((resolve, reject) => {
      this.apiService.post('notification/register-device-token', notificationData).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

  getNotifications() {
    return new Promise((resolve, reject) => {
      this.apiService.getWithHeader('notification/get-notifications').subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }


  updateNotificationStatus(notificationData) {
    return new Promise((resolve, reject) => {
      this.apiService.postWithHeader('notification/change-status', notificationData).subscribe(res => {
        resolve(res);
      }, err => {
        reject(err);
      });
    });
  }

}



