import { Component } from '@angular/core';
import { NavController, NavParams, Events } from 'ionic-angular';
import { FaqDetailsPage } from '../../pages/faq-details/faq-details';


@Component({
  selector: 'page-faq-questions',
  templateUrl: 'faq-questions.html',
})
export class FaqQuestionsPage {
  categoryData;
  questionsList = [];
  constructor(public navCtrl: NavController, public navParams: NavParams, private event: Events) {
    this.categoryData = JSON.parse(navParams.get('category'));
    this.questionsList = this.categoryData['questions'];
    console.log('sdsd', this.questionsList);
  }

  ionViewWillEnter() {
    this.event.publish('trackView', 'On Faq Question list');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FaqQuestionsPage');
  }

  pushQuetionsDetails(que) {
    this.navCtrl.push(FaqDetailsPage, { question: JSON.stringify(que) });
  }
}



